<script>
import { ref } from "vue";
import { buy } from "../secrets/secrets";
import AppButton from "../components/AppButton.vue";
import {
  readWallet,
  copyToClipboard,
  delay,
  storage,
  countDecimals,
  notifyHandler,
  maxBalanceNotify,
  confirmPin,
} from "../composables/handlers";
import { Encrypt, Decrypt, sdkEncoder } from "../composables/cipherHandler";
import { cb_email } from "../secrets/secrets";
import { iconSet } from "../constants/iconSet";
import * as K from "../constants/constants";
import {
  actions,
  Operations,
  bookkeeper_zid,
  distributor_zid,
  issue_zid,
} from "../composables/sdkOperations";
import { useRouter } from "vue-router";
import { readBalance } from "../composables/readBalance";
import { shiuTutLink } from "../constants/appLinks";
import { handleTestPurchase } from "../composables/testNet";
const sanitize = require("htmlsanitize");
import getAccount from "../composables/readAccount";

export default {
  name: "Trade",
  components: { AppButton },
  emits: ["reload"],
  setup(props, context) {
    const router = useRouter();
    const zid = ref(null);
    const balances = ref("");
    const suffix = ref(cb_email().replace("dev.", ""));
    const gasAmount = ref(5);
    const assetAmount = ref({ amount: "0", limit: "50000" });
    const gasLimits = ref([1, 10]);
    const gasValid = ref(true);
    const isPaying = ref(false);
    const needRestore = ref(true);
    const soldOut = ref(true);
    const isLoaded = ref(false);

    const copied = ref(false);

    const loadData = async () => {
      const { accounts, fetchAccount, records } = getAccount();
      if (window.navigator.onLine) {
        await fetchAccount();
        const distBalances = await readBalance(distributor_zid);
        if (
          Number(
            distBalances.filter((e) => {
              return e.asset_code == K.gas;
            })[0].amount
          ) >= 10 &&
          Number(
            distBalances.filter((e) => {
              return e.asset_code == K.asset;
            })[0].amount
          ) >= 2000
        ) {
          soldOut.value = false;
        }
        try {
          const wallet = await readWallet();
          if (wallet.disclose.zid) {
            zid.value = wallet.disclose.zid;
          }
          if (wallet.secure) {
            if (wallet.secure.mnemonic) {
              needRestore.value = false;
            }
          }
          const accountBalances = Decrypt(
            await storage.get("balances"),
            wallet.pack.key
          );
          if (JSON.stringify(accountBalances) == "") {
            balances.value = "";
          } else {
            balances.value = await JSON.parse(accountBalances);
            try {
              assetAmount.value = balances.value.filter((e) => {
                return e.asset_code == K.asset;
              })[0];
              if (JSON.parse(assetAmount.value) == "{}") {
                throw new Error("no asset amount");
              }
            } catch {
              assetAmount.value = { amount: "0", limit: "50000" };
            }
            gasLimits.value[1] = Math.floor(
              balances.value.filter((e) => {
                return e.asset_code == K.asset;
              })[0].amount / 7.8
            );
            if (gasLimits.value[1] < gasAmount.value) {
              gasAmount.value = gasLimits.value[1];
            }
          }
        } catch (err) {
          K.error(err.message);
        }
      }
      isLoaded.value = true;
    };
    loadData();
    const handleCopy = async (zid) => {
      copyToClipboard(zid + suffix.value);
      await loadData();
      copied.value = true;
      await delay(500);
    };
    const clearCopy = () => {
      copyToClipboard(" ");
      if (!isPaying.value) {
        copied.value = false;
      }
    };

    const handlePurchasse = async (selected) => {
      if (!K.devMode) {
        handleCopy(zid.value);
      } else {
        handleCopy(zid.value);
        await handleTestPurchase(selected);
      }
      await loadData();
    };

    const handleDecimal = (value, decimals = 0) => {
      if (Number(value).toString() != "NaN") {
        if (
          countDecimals(value) <= decimals &&
          value >= gasLimits.value[0] &&
          value <= gasLimits.value[1]
        ) {
          gasValid.value = true;
          return true;
        }
      }
      gasValid.value = false;
      return false;
    };

    const confirmPayment = async () => {
      const trans = [
        {
          action: actions.buyOffer,
          xid: "0",
          buy_asset_code: K.gas,
          buy_asset_issuer: K.native,
          sell_asset_code: K.asset,
          sell_asset_issuer: sdkEncoder.zidToxid(issue_zid),
          amount: gasAmount.value.toString(),
          price: 7.8,
        },
      ];
      // let wallet = await readWallet();
      const passFunc = async () => {
        isPaying.value = true;
        try {
          const res = await Operations(trans, `get ${K.gas}`);
          // const res = false;
          if (res == true) {
            isPaying.value = false;
            var shtml = sanitize(`Trade Successful!<br>交易成功！`);
            notifyHandler({
              message: shtml,
              color: "green",
              html: true,
            });
            await router.replace({ name: "Account" });
          } else {
            throw new Error("Transaction Failed!");
          }
        } catch (err) {
          K.error(err.message);
          notifyHandler({
            message: "Transaction Failed!",
            color: "negative",
          });
        }
        isPaying.value = false;
      };
      await confirmPin(passFunc);
      await loadData();
    };

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    return {
      buy,
      zid,
      handleCopy,
      copied,
      clearCopy,
      suffix,
      K,
      iconSet,
      balances,
      gasAmount,
      assetAmount,
      handleDecimal,
      gasLimits,
      gasValid,
      confirmPayment,
      isPaying,
      needRestore,
      soldOut,
      toAccount,
      isLoaded,
      shiuTutLink,
      maxBalanceNotify,
      handlePurchasse,
    };
  },

  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

