<template>
  <div v-if="encryptMode">
    <h4 class="q-mt-none q-mb-md text-weight-bold">RSA Encode</h4>
    <q-input
      bottom-slots
      v-model="message"
      label="Message"
      style="font-size: 1.25rem"
    >
    </q-input>

    <div class="forcewrap" @click="copy(getEncrypted())">
      {{ getEncrypted() }}
    </div>
    <AppButton text="Toggle Encrypt" :action="toggleMode" />
  </div>
  <div v-else>
    <h4 class="q-mt-none q-mb-md text-weight-bold">RSA Decode</h4>
    <q-input
      bottom-slots
      v-model="encode"
      label="Crypto"
      style="font-size: 1.25rem"
    >
    </q-input>

    <div class="forcewrap" @click="copy(decryptMessage())">
      {{ decryptMessage() }}
    </div>
    <AppButton text="Toggle Decrypt" :action="toggleMode" />
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import * as rsa from "../composables/rsaKeys";
import { copyToClipboard } from "../composables/handlers";
import * as K from "../constants/constants";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const key = ref(rsa.newKey());
    const router = useRouter();
    const message = ref("");
    const encode = ref("");
    const secretKey = ref("");
    const encryptMode = ref(true);

    const loadData = async () => {
      try {
        key.value = await rsa.getKeys();
      } catch {
        key.value = rsa.newKey();
      }
    };
    loadData();

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const toAudit = () => {
      router.replace({ name: "Audit" });
    };

    const getEncrypted = () => {
      if (encryptMode.value == true && message.value != "") {
        const encrypted = rsa.rsaEncrypt(key.value, message.value, false);
        return encrypted;
      }
    };

    const decryptMessage = () => {
      const decode = rsa.rsaDecrypt(key.value, encode.value, false);
      return decode;
    };

    const toggleMode = () => {
      encryptMode.value = !encryptMode.value;
    };

    const copy = async (val) => {
      copyToClipboard(val);
    };

    return {
      message,
      encode,
      secretKey,
      getEncrypted,
      toggleMode,
      encryptMode,
      decryptMessage,
      copy,
    };
  },
};
</script>

