<template>
  <div>
    <h4 id="header" class="q-mt-none q-mb-md text-weight-bold">保育鏗鏘集</h4>
    <div>
      <div v-if="!hasNetwork">
        <p>No network, please retry</p>
        <p>冇網絡 請再試</p>
        <div>
          <AppButton text="Back 返去" :action="toAccount" />
        </div>
      </div>
      <div v-else>
        <q-list>
          <q-item v-for="a in thecommonsense" :key="sanitize(a.cid)">
            <div
              :id="a.cid"
              class="q-px-none q-pb-sm row items-start q-gutter-md"
            >
              <q-card class="my-card" @click="openCard(a)">
                <q-img
                  :src="`${dataLink}thumbnails/${
                    a.thumbnail.split('https://')[1].split('.ipfs.dweb.link')[0]
                  }.png`"
                >
                  <div class="absolute-top text-subtitle2 text-right">
                    鏗鏘集 The Common Sense
                  </div>
                </q-img>

                <q-card-section>
                  <div
                    class="text-h6"
                    :class="{
                      forcewrap: wrapTitle(sanitize(a.name)),
                    }"
                  >
                    {{ sanitize(a.name) }}
                  </div>
                </q-card-section>
              </q-card>
            </div>
          </q-item>
        </q-list>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { thecommonsense } from "../constants/thecommonsense";
const sanitize = require("htmlsanitize");
import { flashThumbnail } from "../composables/handlers";
import { dataLink } from "../secrets/secrets";

export default {
  components: { AppButton },
  setup() {
    const router = useRouter();
    const hasNetwork = ref(true);

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const loadData = () => {
      if (window.navigator.onLine) {
        hasNetwork.value = true;
      } else {
        hasNetwork.value = false;
      }
    };

    loadData();

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    const openCard = async (item) => {
      let back = { name: "IPFSVideos" };
      await router.replace({
        name: "IPFSVideo",
        params: { id: item.cid, back: JSON.stringify(back) },
      });
    };

    return {
      toAccount,
      sanitize,
      hasNetwork,
      wrapTitle,
      openCard,
      dataLink,
      thecommonsense,
    };
  },

  mounted() {
    var section = this.$router.currentRoute.value.hash.replace("#", "");
    if (section) {
      this.$nextTick(() => {
        try {
          window.document.getElementById(section).scrollIntoView();
        } catch {}
      });
    }
  },
};
</script>

<style scoped>
.forcewrap {
  word-break: break-all;
}
.infoText {
  font-size: 1.25rem;
  font-weight: bold;
}
.my-card {
  width: 100%;
  width: calc(90vw);
  max-width: 700px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
</style>