<template>
  <div v-if="!card">
    <div v-show="!loaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-show="loaded">
      <div v-if="!user || !isInstalled">
        <h4 class="q-mt-none q-mb-md text-weight-bold">Hi! 食咗飯未!</h4>
        <p>
          Free Hong Koin (FreeHK) is powered by distributed web. A secure,
          robust and distributed approach to manage assets.
        </p>
        <p>
          自由港幣由去中心化網絡驅動。安全、可靠、公開、透明，唔洗靠金管局。
        </p>
        <AppButton text="About 了解吓" :action="toAbout" />
        <div style="text-align: center">
          <div v-if="isInstalled">
            <UserAuth />
            <div>
              <span class="NgMoDieWholeFamily">5mo DLLM</span>
            </div>
            <div>
              <span class="NgMoDieWholeFamily">5毛與狗不得內進</span>
            </div>
          </div>
          <div v-else>
            <AppButton text="Install 安装" :action="installHandler" />
          </div>
        </div>
      </div>
      <div v-else>
        <div v-if="!wallet.disclose">
          <Options @reload="reloadInfo" />
        </div>
        <div v-else-if="!wallet.disclose.zid">
          <h4 class="q-mt-none q-mb-md text-weight-bold">Wallet 荷包</h4>
          <p>
            Please get some pen and paper to write down your 12-word mnemonic,
            keep it safe and never reveal it to anyone!
          </p>
          <p>請準備紙筆寫低你嘅12字密䃙，千萬要小心保管唔好俾人知。</p>
          <CreateWallet @reload="reloadInfo" />
        </div>
        <div v-else>
          <span v-if="username">
            <h5
              class="q-mt-none q-mb-none text-weight-bold text-center"
              :class="{ forcewrap: wrapUsername }"
            >
              {{ username }}
            </h5>
          </span>
          <span v-else>
            <h4 class="q-mt-none q-mb-md text-weight-bold">Balances</h4>
          </span>
          <div
            v-if="isLocked"
            style="text-align: center"
          >
            <div @click="unlockMe">
              <q-img
                :src="lockstring.img"
                :style="`height: ${(
                  Math.min(windowHeight * 0.4 * 2, 0.9 * windowWidth) / 2
                ).toFixed(1)}px; width: ${Math.min(
                  windowHeight * 0.4 * 2,
                  0.9 * windowWidth
                ).toFixed(1)}px`"
              >
              </q-img>
            </div>
            <div>
              <p>{{ lockstring.en }}</p>
              <p>{{ lockstring.hk }}</p>
            </div>

            <q-footer>
              <div
                class="row q-px-lg q-mt-sm justify-between btnStyle"
                style="max-width: 700px"
              >
                <div class="q-my-sm q-mb-md text-center">
                  <q-btn
                    outline
                    rounded
                    color="white"
                    @click="toReceive"
                    no-caps
                  >
                    <div v-show="windowWidth >= 360">
                      <q-icon color="white" name="south_east" />
                      <q-icon color="white" :name="iconSet[K.asset]" />
                    </div>
                    <span :class="{ 'q-pl-sm': windowWidth >= 360 }"
                      >Take 收</span
                    >
                  </q-btn>
                </div>
                <div class="q-my-sm q-mb-md text-center btnStyle">
                  <q-btn
                    outline
                    rounded
                    color="white"
                    @click="unlockMe"
                    no-caps
                  >
                    <span :class="{ 'q-pr-sm': windowWidth >= 360 }"
                      >Unlock 解鎖</span
                    >
                    <div v-show="windowWidth >= 360">
                      <!-- <q-icon color="white" :name="iconSet[K.asset]" /> -->
                      <q-icon color="white" name="lock_open" />
                    </div>
                  </q-btn>
                </div>
              </div>
            </q-footer>
          </div>
          <div v-else>
            <Balances @reloadAccount="reloadInfo" />
          </div>
          <div v-show="!isLocked">
            <div class="q-my-sm q-mb-md text-center btnStyle">
              <q-btn filled rounded color="primary" @click="lockMe" no-caps>
                <span :class="{ 'q-pr-sm': windowWidth >= 360 }"
                  >Lock 上鎖</span
                >
                <q-icon color="white" name="lock" />
              </q-btn>
            </div>
            <div v-if="isDesktop && false">
              <AppButton text="Hardware Wallet" :action="getStrPublicKey" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-else>
    <p class="head">Desktop 電腦</p>
    <p>
      1. Go to
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
      using Chrome.
    </p>
    <p>2. Press "Install".</p>
    <p>
      1. 用Chrome打開
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
    </p>
    <p>2. 㩒 "安裝".</p>
    <p class="head">Android 手提</p>
    <p>
      1. Go to
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
      using Chrome.
    </p>
    <p>2. Press "Install app".</p>
    <p>
      1. 用Chrome打開
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
    </p>
    <p>2. 㩒 "安裝應用程式".</p>
    <p class="head">iOS 手機</p>
    <p>
      1. Go to
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
      using Safari.
    </p>
    <p>2. Press "Add to Home Screen".</p>
    <p>
      1. 用Safari打開
      <a target="_blank" :href="site"
        ><span class="clickSpan">{{ K.issuer }}</span></a
      >
    </p>
    <p>2. 㩒 "加至主畫面".</p>
    <AppButton text="Back 返去" :action="goBack" />
  </div>
</template>

<script>
import { ref } from "vue";
import UserAuth from "../components/UserAuth.vue";
import { useRouter } from "vue-router";
import getUser from "../composables/getUser";
import CreateWallet from "../components/CreateWallet.vue";
import Options from "../components/Options.vue";
import Balances from "../components/Balances.vue";
import { Encrypt, Decrypt } from "../composables/cipherHandler";
import {
  hkDateText,
  readWallet,
  getPWADisplayMode,
  storage,
  dialogHandler,
  confirmPin,
  notifyHandler,
  isDesktop,
} from "../composables/handlers";
import * as K from "../constants/constants";
import AppButton from "../components/AppButton.vue";
import { dataLink } from "../secrets/secrets";
const axios = require("axios");
import { iconSet } from "../constants/iconSet";
import { getStrAppVersion, getStrPublicKey } from "../composables/hwsupport";
import getAccount from "../composables/readAccount";
import {
  trustLimit,
  Operations,
  issue_zid,
  actions,
} from "../composables/sdkOperations";
import { site } from "../constants/appLinks";
import { locklogs, actual_bd, official_list } from "../constants/locklogs";

export default {
  name: "Account",
  components: { UserAuth, CreateWallet, Options, Balances, AppButton },
  emits: ["reload"],
  setup(props, context) {
    const loaded = ref(false);
    const { user } = getUser();
    const username = ref(null);
    const wrapUsername = ref(false);
    const wallet = ref({});
    const isInstalled = ref(false);
    const card = ref(false);
    const router = useRouter();
    const isLocked = ref(false);
    const appBlock = ref(false);
    const lockstring = ref(locklogs[0]);

    const reloadInfo = async () => {
      const appStatus = getPWADisplayMode();
      if (
        appStatus == "standalone" ||
        (process.env.NODE_ENV != "production" && K.devMode)
      ) {
        isInstalled.value = true;
      }
      wallet.value = await readWallet();

      if (wallet.value == {}) {
        return;
      }
      let env = "";
      if (K.devMode) {
        env = "dev/";
      }
      try {
        const url = `${dataLink}${env}blocklist/${wallet.value.disclose.zid}.json`;
        let response = await axios.get(url);
        let responseJson = response.data;
        K.print(responseJson);
        let message = "Account is on blocklist.";
        K.error(message);
        throw new Error(message);
      } catch (err) {
        if (err.message == "Account is on blocklist.") {
          notifyHandler({
            message: "Account is blocked!",
            color: "negative",
          });
          loaded.value = true;
          appBlock.value = true;
          await lockApp();
          return;
        }
      }
      if (wallet.value.pack) {
        if (wallet.value.pack.key) {
          try {
            const { accounts, fetchAccount, records } = getAccount();
            const url = `${dataLink}${env}allowlist/${wallet.value.disclose.zid}.json`;
            let response = await axios.get(url);
            let responseJson = response.data;
            K.print(responseJson);
            K.print("User is on allow list.");
            await fetchAccount();
            let accountBalances = await JSON.parse(
              Decrypt(await storage.get("balances"), wallet.value.pack.key)
            );
            try {
              accountBalances = accountBalances.filter((b) => {
                return b.asset_code == K.asset && b.asset_issuer == K.issuer;
              });
            } catch {
              accountBalances = {};
            }
            if (JSON.stringify(accountBalances) != "{}") {
              if (Number(accountBalances[0].limit) < Number(trustLimit.MAX)) {
                let trans = [
                  {
                    action: actions.trust,
                    xid: issue_zid,
                    asset_code: K.asset,
                    amount: trustLimit.MAX,
                  },
                ];
                await Operations(trans, "allow max");
              }
            }
          } catch {
            K.print("User is not on allow list.");
            const { accounts, fetchAccount, records } = getAccount();
            await fetchAccount();
            let accountBalances;
            try {
              accountBalances = await JSON.parse(
                Decrypt(await storage.get("balances"), wallet.value.pack.key)
              );
              accountBalances = accountBalances.filter((b) => {
                return b.asset_code == K.asset && b.asset_issuer == K.issuer;
              });
            } catch {
              accountBalances = {};
            }
            if (JSON.stringify(accountBalances) != "{}") {
              if (Number(accountBalances[0].limit) > Number(trustLimit.POS)) {
                if (
                  Number(accountBalances[0].amount) <= Number(trustLimit.POS)
                ) {
                  let trans = [
                    {
                      action: actions.trust,
                      xid: issue_zid,
                      asset_code: K.asset,
                      amount: trustLimit.POS,
                    },
                  ];
                  await Operations(trans, "limit pos");
                } else {
                  let trans = [
                    {
                      action: actions.trust,
                      xid: issue_zid,
                      asset_code: K.asset,
                      amount: accountBalances[0].amount,
                    },
                  ];
                  await Operations(trans, "limit amount");
                }
              }
            }
          }
        }
      }

      if (appBlock.value == false) {
        try {
          wallet.value = await readWallet();
          if (wallet.value.secure.mnemonic) {
            if (wallet.value.secure.locked) {
              isLocked.value = true;
              try {
                lockstring.value = wallet.value.secure.lockstring;
              } catch (err) {
                K.error(err);
              }
            }
          } else {
            if (wallet.value.disclose.zid) {
              K.print("go to mnemonic");
              await router.replace({
                name: "Mnemonic",
                params: {
                  mnemonic: "Please restore mnemonic 請光復密䃙",
                  backTo: "Account",
                },
              });
            }
          }
        } catch {
          K.print("app started");
        }
      }

      loaded.value = false;
      if (wallet.value.disclose) {
        username.value = wallet.value.disclose.username;
        const txt = username.value;
        const tmpArr = txt.split(" ");
        const arrLen = tmpArr.map((e) => {
          return e.length;
        });
        const maxLength = Math.max(...arrLen);
        if (maxLength > 20) {
          wrapUsername.value = true;
        }
      }
      context.emit("reload");
      loaded.value = true;
    };
    reloadInfo();

    const installHandler = () => {
      const appStatus = getPWADisplayMode();
      if (appStatus == "standalone") {
        isInstalled.value = true;
      } else {
        card.value = true;
      }
    };

    const goBack = async () => {
      await reloadInfo();
      card.value = false;
    };

    const toAbout = () => {
      router.replace({ name: "About" });
    };

    const lockMe = async () => {
      if (
        hkDateText().slice(5, 10) == actual_bd ||
        official_list.includes(hkDateText().slice(0, 10))
      ) {
        const log = Math.floor(Math.random() * 2) + 2;
        lockstring.value = locklogs[log];
      } else {
        const log = Math.floor(Math.random() * 2);
        lockstring.value = locklogs[log];
      }
      K.print(lockstring.value);
      dialogHandler({
        title: "Confirm Lock 確認上鎖?",
        message: "Security Pin is required to unlock.<br>解鎖須要保安編䃙。",
        persistent: true,
        ok: {
          label: "Understand 明",
          style: { "text-transform": "none" },
          color: "green",
        },
        cancel: {
          label: "Cancel 取消",
          style: { "text-transform": "none" },
        },
        html: true,
      }).onOk(async () => {
        await lockApp();
      });
    };

    const lockApp = async () => {
      const wallet = await readWallet();
      const key = wallet.pack.key;
      isLocked.value = true;
      let secure = wallet.secure;
      secure["locked"] = true;
      secure["lockstring"] = lockstring.value;
      await storage.set("secure", await Encrypt(secure, key));
      context.emit("reload");
    };

    const unlockMe = async () => {
      const passFunc = async () => {
        isLocked.value = false;
        await reloadInfo();
        context.emit("reload");
      };
      const failFunc = async () => {
        await reloadInfo();
      };
      if (appBlock.value == false) {
        await confirmPin(passFunc, failFunc);
      } else {
        dialogHandler({
          title: "You have been blocked<br>你正被封鎖",
          message: "You have violated our terms.<br>你違反用戶守則。",
          persistent: true,
          ok: {
            label: "OK 得",
            style: { "text-transform": "none" },
            color: "green",
          },
          html: true,
        }).onOk(async () => {
          window.location.reload(true);
        });
      }
    };

    const toReceive = () => {
      router.replace({ name: "Take" });
    };

    return {
      isInstalled,
      loaded,
      user,
      wallet,
      reloadInfo,
      username,
      wrapUsername,
      K,
      site,
      installHandler,
      card,
      toAbout,
      goBack,
      isLocked,
      lockMe,
      unlockMe,
      toReceive,
      iconSet,
      getStrPublicKey,
      isDesktop,
      lockstring,
    };
  },
  data: function () {
    return {
      windowWidth: 0,
      windowHeight: 0,
    };
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.getWindowWidth);
      window.addEventListener("resize", this.getWindowHeight);
      this.getWindowWidth();
      this.getWindowHeight();
    });
  },
  methods: {
    getWindowWidth(event) {
      this.windowWidth = document.documentElement.clientWidth;
    },
    getWindowHeight(event) {
      this.windowHeight = document.documentElement.clientHeight;
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.getWindowWidth);
    window.removeEventListener("resize", this.getWindowHeight);
  },
};
</script>
<style scoped>
.forcewrap {
  word-break: break-all;
}
.NgMoDieWholeFamily {
  color: grey;
}
.head {
  font-weight: bold;
}
.clickSpan {
  cursor: pointer;
  text-decoration: underline;
}
.infoText {
  font-size: 1rem;
}
.assetText {
  font-size: 1.25rem;
}
.btnStyle button {
  min-width: 40%;
  font-size: 1rem;
}
