<template>
  <div>
    <q-pull-to-refresh @refresh="refresh">
      <p class="infoText">{{ asset.name }}</p>
      <div
        class="align-center justify-center"
        style="width: 90vw; max-width: 700px"
      >
        <q-video
          :ratio="16 / 9"
          auto="false"
          width="100%"
          :src="asset.link"
          frameborder="0"
          allowfullscreen
        />
        <div class="align-center justify-center"></div>
      </div>
      <div class="q-mt-md">
        <p>就連 upload 咗上 YouTube 嘅片，都可以俾人黃標刪片封網。所以我哋選擇用
          <a href="https://ipfs.io/" target="_blank"
            ><span class="clickSpan">IPFS</span>*
          </a>
          upload 啲片上 distributed web 度。
        </p>
        <p>而因為去中心化網絡真正冇大台，咁樣中共就冇辨法封網喇。</p>
        <p>大家都一齊活用 distributed web 對抗極權啦！</p>
      </div>
      <div>
        <AppButton text="Back 返去" :action="goBack" />
      </div>
      <p>
        <span class="miniText"
          >*
          <a :href="`https://${domain}`" target="_blank"
            ><span class="clickSpan miniText">FreeHK.io</span>
          </a>
          is a 5DN1L initiative built around Web3</span
        >
      </p>
    </q-pull-to-refresh>
  </div>
</template>

<script>
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { thecommonsense } from "../constants/thecommonsense";
const sanitize = require("htmlsanitize");
import { ref } from "vue";
import { domain } from "../constants/appLinks";

export default {
  components: { AppButton },
  props: ["id", "back"],
  setup(props) {
    const router = useRouter();
    const asset = ref({});

    const loadData = async () => {
      const item = thecommonsense.filter((c) => {
        return c.cid == props.id;
      });
      if (item.length > 0) {
        asset.value = item[0];
        asset.value["link"] = `https://${asset.value.cid}.ipfs.dweb.link/`;
      } else {
        router.replace({ name: "IPFSVideos" });
      }
    };

    loadData();

    const goBack = () => {
      router.replace(`/thecommonsense#${props.id}`);
    };

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    return {
      goBack,
      domain,
      thecommonsense,
      loadData,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
      sanitize,
      wrapTitle,
      asset,
    };
  },
};
</script>

