<template>
  <div>
    <q-pull-to-refresh @refresh="refresh">
      <p class="infoText">{{ asset.name }}</p>
      <div
        class="align-center justify-center"
        style="width: 90vw; max-width: 700px"
        v-if="loaded"
      >
        <q-video
          :ratio="16 / 9"
          auto="false"
          width="100%"
          :src="asset.link"
          frameborder="0"
          allowfullscreen
        />
        <div class="align-center justify-center"></div>
      </div>
      <div v-if="loaded" class="q-mt-md">
        <p>{{ asset.description }}</p>
      </div>
      <div>
        <AppButton text="Back 返去" :action="goBack" />
      </div>
      <p>
        <span class="miniText"
          >*
          <a :href="`https://${domain}`" target="_blank"
            ><span class="clickSpan miniText">FreeHK.io</span>
          </a>
          is a 5DN1L initiative built around Web3</span
        >
      </p>
    </q-pull-to-refresh>
  </div>
</template>

<script>
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { thecommonsense } from "../constants/thecommonsense";
const sanitize = require("htmlsanitize");
import { ref } from "vue";
import { domain } from "../constants/appLinks";
const axios = require("axios");
import { getCidLinks, locateData } from "../composables/handlers";

export default {
  components: { AppButton },
  props: ["id", "back"],
  setup(props) {
    const router = useRouter();
    const asset = ref({});
    const loaded = ref(false);

    const loadData = async () => {
      try {
        let response;
        try {
          response = await axios.get(locateData(props.id));
        } catch {
          response = await axios.get(`https://${props.id}.ipfs.dweb.link/`);
        };
        let responseJson = response.data;

        if (responseJson) {
          asset.value = responseJson;
          const linked = getCidLinks(asset.value.image);
          asset.value["link"] = linked.link1;
        } else {
          router.replace({ name: "Tokens" });
        }
      } catch {
        router.replace({ name: "Tokens" });
      }
      loaded.value = true;
    };

    loadData();

    const goBack = () => {
      router.replace(`/tokens`);
    };

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    return {
      goBack,
      domain,
      thecommonsense,
      loadData,
      loaded,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
      sanitize,
      wrapTitle,
      asset,
    };
  },
};
</script>

