import { getAccount } from "../composables/cipherHandler";
import { readWallet } from "../composables/handlers";
import * as K from "../constants/constants";
import { transferPhrase } from "../secrets/secrets";
import getUser from "../composables/getUser";
var axios = require("axios");
import { readBalance } from "../composables/readBalance";
import { sdkEncoder } from "../composables/cipherHandler";
import { actions, trustLimit, Operations, issue_zid } from "../composables/sdkOperations";

const { user } = getUser();


export const handleTestPurchase = async(testPurchase = "COMBO") => {

    if (K.devMode) {
        await createTestAccount();
        const wallet = await readWallet();
        const entropy = user._value.uid + wallet.pack.salt + transferPhrase;
        const newAccount = getAccount(wallet.secure.mnemonic, entropy, 3);
        const zeed = sdkEncoder.xidTozid(newAccount.seed);
        let trans = [];
        switch (testPurchase) {
            case "COMBO":
                const balances = await readBalance(wallet.disclose.zid);
                if (balances.length == 0) {
                    trans = [{
                            action: actions.create,
                            xid: wallet.disclose.zid,
                            asset_code: K.gas,
                            amount: "5.0000200"
                        },
                        {
                            action: actions.claimable,
                            xid: wallet.disclose.zid,
                            asset_code: `${K.asset}:${sdkEncoder.zidToxid(issue_zid)}`,
                            amount: '50'
                        },
                    ]
                } else {
                    trans = [{
                            action: actions.payment,
                            xid: wallet.disclose.zid,
                            asset_code: K.gas,
                            amount: '5',
                        },
                        {
                            action: actions.payment,
                            xid: wallet.disclose.zid,
                            asset_code: K.asset,
                            amount: '50',
                        },
                    ]
                };
                break;
            case "FreeHongKoin100":
                trans = [{
                    action: actions.payment,
                    xid: wallet.disclose.zid,
                    asset_code: K.asset,
                    amount: '100',
                }, ]
                break;
            case "FreeHongKoin500":
                trans = [{
                    action: actions.payment,
                    xid: wallet.disclose.zid,
                    asset_code: K.asset,
                    amount: '500',
                }, ]
                break;
            case "FreeHongKoin1000":
                trans = [{
                    action: actions.payment,
                    xid: wallet.disclose.zid,
                    asset_code: K.asset,
                    amount: '1000',
                }, ]
                break;
        };
        await Operations(trans, testPurchase, "", true, zeed);
    };
};

export const createTestAccount = async(n = 3) => {
    let testAccount = {};
    if (K.devMode) {
        const wallet = await readWallet();
        const entropy = user._value.uid + wallet.pack.salt + transferPhrase;
        const newAccount = getAccount(wallet.secure.mnemonic, entropy, n);
        const zid = sdkEncoder.xidTozid(newAccount.xid);
        const zeed = sdkEncoder.xidTozid(newAccount.seed);
        let balances = await readBalance(zid);
        if (balances.length == 0) {
            await fundTestAccount(newAccount.xid);
            if (n != 2) {
                await createTestAccount(2);
            };
        };
        if (n == 2) {
            let trans = [{
                action: actions.merge,
                xid: sdkEncoder.xidTozid(getAccount(wallet.secure.mnemonic, entropy, 3).xid),
            }, ]
            await Operations(trans, "merge account", "", true, zeed);
        }
        if (n > 2 && balances.length <= 1) {
            let trans = [{
                action: actions.trust,
                xid: issue_zid,
                asset_code: K.asset,
                amount: trustLimit.MAX,
            }];
            await Operations(trans, "add trust", "", true, zeed);
            balances = await readBalance(zid);
        };
        let asset = balances.filter((balance) => {
            return balance.asset_code == K.asset && balance.asset_issuer == issue_zid
        });
        if (n == 3 && asset.length > 0) {
            let amount = 1000;
            if ((parseFloat(asset[0].amount) - 1000) < 0) {
                await createTestAccount(2);
                let trans = [{
                    action: actions.buyOffer,
                    xid: "0",
                    buy_asset_code: K.asset,
                    buy_asset_issuer: issue_zid,
                    sell_asset_code: K.gas,
                    sell_asset_issuer: K.native,
                    amount: amount.toString(),
                    price: 10,
                }];
                await Operations(trans, "offer to buy asset", "", true, zeed);
            };

        };
        testAccount = { zid, zeed };
    };
    return testAccount;
};

export const fundTestAccount = async(xid) => {
    if (K.devMode) {
        try {
            let url = `https://friendbot.stellar.org?addr=${xid}`;
            const response = await axios.get(url);
            let responseJson = response.data;
            K.print("SUCCESS! You have a new account!");
        } catch (e) {
            K.error("ERROR!", e);
        };
    };
};