import * as K from "../constants/constants";
import * as SH from "../secrets/sdkHandler";
import { assetDefinition } from '../secrets/secrets';
import { readWallet, notifyHandler } from "../composables/handlers";
import { sdkEncoder, makeHash } from './cipherHandler';
import getAccount from "../composables/readAccount";

const sdk = SH.sdk;
const networkPassphrase = SH.networkPassphrase;
const server = SH.newServer();

export const actions = { create: 'create', payment: 'payment', claimable: 'claimable', claim: 'claim', trust: 'trust', data: 'data', buyOffer: 'buyOffer', sellOffer: 'sellOffer', merge: 'merge', sendAsset: 'sendAsset' };
export const trustLimit = { MAX: "922337203685.4775807", MIN: "0.0000001", NIL: "0", POS: "50000" };
export const issue_zid = assetDefinition[K.asset].asset_issuer;
export const distributor_zid = assetDefinition[K.asset].asset_distributor;
export const auctioneer_zid = assetDefinition.auctioneer;
export const bookkeeper_zid = assetDefinition.bookkeeper;
export const nftmanager_zid = assetDefinition.nftmanager;
export const nftissuer_zid = assetDefinition.nftissuer;

export const gasGuard = async(ops) => {
    const { accounts, fetchAccount, records } = getAccount();
    await fetchAccount();
    let min_balance;
    let gas_amount;
    accounts.value.map(e => {
        if (e.asset_code == K.gas) {
            min_balance = e.min_balance;
            gas_amount = e.amount;
        };
    });
    const fee = await SH.fee();
    let gas_needed = (fee * ops.length * trustLimit.MIN).toFixed(7);

    ops.forEach(e => {
        switch (e.action) {
            case actions.create:
                gas_needed = Number(gas_needed) + Number(e.amount)
                break;
            case actions.payment:
                if (e.asset_code == K.gas) {
                    gas_needed = Number(gas_needed) + Number(e.amount)
                }
                break;
            case actions.claimable:
                gas_needed = Number(gas_needed) + 0.5
                break;
            case actions.trust:
                if (e.amount > 0) {
                    gas_needed = Number(gas_needed) + 0.5
                }
                break;
            case actions.data:
                gas_needed = Number(gas_needed) + 0.5
                break;
            case actions.buyOffer:
                gas_needed = Number(gas_needed) + 0.5
                break;
            case actions.sellOffer:
                gas_needed = Number(gas_needed) + 0.5
                break;
            default:
                // code block
        };
    });
    gas_needed = Number(gas_needed).toFixed(7);
    if (Number(gas_amount) - Number(min_balance) > Number(gas_needed)) {
        return true;
    } else {
        notifyHandler({
            message: `Gas is not enough, please trade some!<br> 唔夠氣，請加氣！`,
            color: "warning",
            html: true,
        });
        return false
    };
};

export const Operations = async(ops, send_memo, signer = "", completeSet = true, zeed = "") => {
    let err = null;
    let added = false;
    let trans = [];
    let signagures = [];
    try {
        let gasValid = true;
        if (zeed == "") {
            gasValid = await gasGuard(ops);
        };
        if (!gasValid) {
            return false;
        };
        const wallet = await readWallet();
        let source_secret = sdkEncoder.zidToxid(wallet.secure.zeed, true);
        if (zeed != "" && K.devMode) {
            source_secret = sdkEncoder.zidToxid(zeed, true);
        };
        const issue_xid = sdkEncoder.zidToxid(issue_zid);
        const GAS = sdk.Asset.native();
        let sourceKeys = sdk.Keypair.fromSecret(source_secret);
        let signerKeys = sdk.Keypair.fromSecret(source_secret);
        if (signer != "") {
            signerKeys = sdk.Keypair.fromSecret(sdkEncoder.zidToxid(signer, true));
        };
        signagures.push(signerKeys);
        const sourceAccount = await server.loadAccount(sourceKeys.publicKey());
        const fee = await SH.fee();
        const memoTxt = send_memo;
        let opMemo = sdk.Memo.text("");
        if (memoTxt.length > 28) {
            try {
                const hash = makeHash.qh(memoTxt);
                opMemo = sdk.Memo.hash(hash);
            } catch {
                const message = 'Memo text is not valid.';
                K.error(message);
                throw new Error(message);
                return;
            }
        } else {
            opMemo = sdk.Memo.text(memoTxt);
        };

        let transaction = new sdk.TransactionBuilder(sourceAccount, {
                fee,
                networkPassphrase,
            }).addMemo(opMemo)
            .setTimeout(SH.timeout);

        ops.forEach((op) => {
            let muxing = false;
            if (op.muxpin) {
                muxing = true;
            };
            let destination = op.xid;
            if (op.action == actions.create ||
                op.action == actions.payment ||
                op.action == actions.claimable ||
                op.action == actions.trust ||
                op.action == actions.merge) {
                try {
                    K.print("in operations destination");
                    destination = addressGuard(destination);
                    if (destination == null) {
                        throw new Error('Invalid destionation address.');
                    };
                } catch {
                    const message = 'Invalid destionation address.';
                    K.error(message);
                    throw new Error(message);
                };
            };
            const amount = op.amount;
            let asset = GAS;
            if (op.action != actions.data &&
                op.action != actions.claim &&
                op.action != actions.merge &&
                op.action != actions.buyOffer &&
                op.action != actions.sellOffer) {
                if (op.asset_code.length <= 12 && op.asset_code != K.gas) {
                    asset = new sdk.Asset(op.asset_code, issue_xid);
                };
            };
            try {
                if (op.action == actions.payment) {
                    if (op.asset_code == K.asset || op.asset_code == K.gas) {
                        if (op.asset_code == K.asset && destination == issue_xid) {
                            const message = 'Cannot send to asset issuer.';
                            K.error(message);
                            throw new Error(message);
                        } else {
                            if (sourceKeys.publicKey() != destination) {
                                try {
                                    if (muxing) {
                                        const muxed = new sdk.MuxedAccount(new sdk.Account(destination, '0'), op.muxpin.toString());
                                        destination = muxed.accountId();
                                    }
                                    let source = sourceKeys.publicKey();
                                    if (op.source_key) {
                                        try {
                                            source = sdk.Keypair.fromSecret(op.source_key).publicKey();
                                            signagures.push(sdk.Keypair.fromSecret(sdkEncoder.zidToxid(op.source_key, true)));
                                        } catch {
                                            throw new Error('Payment source key is not valid.');
                                        };
                                    };
                                    transaction.addOperation(sdk.Operation.payment({
                                        destination,
                                        asset,
                                        amount,
                                        withMuxing: muxing,
                                        source: source,
                                    }));
                                    added = true;
                                    trans.push(op);
                                } catch (e) {
                                    K.error(e.message);
                                };
                            } else {
                                const message = 'Cannot send asset to oneself.';
                                K.error(message);
                                throw new Error(message);
                            };
                        };
                    } else {
                        const message = 'Invalid payment of asset.';
                        K.error(message);
                        throw new Error(message);
                    };
                } else if (op.action == actions.sendAsset) {
                    if (muxing) {
                        const muxed = new sdk.MuxedAccount(new sdk.Account(destination, '0'), op.muxpin.toString());
                        destination = muxed.accountId();
                    };
                    let source = sourceKeys.publicKey();
                    if (op.source_key) {
                        try {
                            source = sdk.Keypair.fromSecret(op.source_key).publicKey();
                            signagures.push(sdk.Keypair.fromSecret(sdkEncoder.zidToxid(op.source_key, true)));
                        } catch {
                            throw new Error('Send asset source key is not valid.');
                        };
                    };
                    try {
                        sdk.Keypair.fromPublicKey(addressGuard(op.issuer));
                        if (op.asset_code != K.gas && op.asset_code.length <= 12) {
                            asset = new sdk.Asset(op.asset_code, addressGuard(op.issuer));
                        } else {
                            throw new Error('Asset code is not valid.');
                        };
                    } catch {
                        throw new Error('Asset is not valid.');
                    };
                    transaction.addOperation(sdk.Operation.payment({
                        destination,
                        asset,
                        amount,
                        withMuxing: muxing,
                        source: source,
                    }));
                    added = true;
                    trans.push(op);

                } else if (op.action == actions.create) {
                    transaction.addOperation(sdk.Operation.createAccount({
                        destination,
                        startingBalance: amount // gas
                    }));
                    added = true;
                    trans.push(op);
                } else if (op.action == actions.claimable) {
                    const asset_codes = op.asset_code.split(":");
                    if (asset_codes.length == 2) {
                        if (asset_codes[0] != K.asset) {
                            try {
                                const pair = sdk.Keypair.fromPublicKey(
                                    sdkEncoder.zidToxid(asset_codes[1]),
                                );
                                if (destination != pair.publicKey()) {
                                    if (asset_codes[0].length <= 12) {
                                        asset = new sdk.Asset(asset_codes[0], pair.publicKey());
                                    } else {
                                        const message = 'Invalid NFT asset code.';
                                        K.error(message);
                                        throw new Error(message);
                                    };
                                } else {
                                    const message = 'Cannot send NFT to issuer.';
                                    K.error(message);
                                    throw new Error(message);
                                };
                            } catch {
                                const message = 'Invalid NFT asset issuer ID.';
                                K.error(message);
                                throw new Error(message);
                            };
                        } else {
                            asset = new sdk.Asset(asset_codes[0], issue_xid);
                        };
                    } else if (asset_codes.length == 1) {
                        if (asset_codes[0] != K.asset && asset_codes[0] != K.gas) {
                            const message = 'Invalid asset code.';
                            K.error(message);
                            throw new Error(message);
                        };
                    } else if (asset_codes.length > 2) {
                        const message = 'Too many asset attributes.';
                        K.error(message);
                        throw new Error(message);
                    };

                    let passTime = 0;
                    if (op.passTime) {
                        passTime = op.passTime;
                    };
                    let soon = (Math.ceil((Date.now() / 1000) + passTime)).toString();
                    let beforeDeadline = sdk.Claimant.predicateBeforeAbsoluteTime(soon);
                    let afterDeadline = sdk.Claimant.predicateNot(
                        beforeDeadline
                    );
                    let claimants = [
                        new sdk.Claimant(destination, afterDeadline),
                        new sdk.Claimant(sourceKeys.publicKey(), afterDeadline),
                    ];
                    if (op.claimants) {
                        claimants = op.claimants;
                    };
                    try {
                        transaction.addOperation(sdk.Operation.createClaimableBalance({
                            claimants,
                            asset,
                            amount,
                        }));
                        added = true;
                        trans.push(op);
                    } catch {
                        const message = 'Invalid claimants setup.';
                        K.error(message);
                        throw new Error(message);
                    };
                } else if (op.action == actions.trust) {
                    let limit = amount;
                    switch (amount) {
                        case "MAX":
                            limit = trustLimit.MAX;
                            break;
                        case "MIN":
                            limit = trustLimit.MIN;
                            break;
                        case "NIL":
                            limit = trustLimit.NIL;
                            break;
                        default:
                            limit = amount;
                    };
                    asset = new sdk.Asset(op.asset_code, destination);
                    transaction.addOperation(
                        sdk.Operation.changeTrust({
                            asset,
                            limit
                        }),
                    );
                    added = true;
                    trans.push(op);
                } else if (op.action == actions.data) {
                    transaction.addOperation(
                        sdk.Operation.manageData({
                            name: op.asset_code,
                            value: op.amount
                        }),
                    );
                    added = true;
                    trans.push(op);
                } else if (op.action == actions.claim) {
                    const balanceId = destination;
                    let claimBalance = sdk.Operation.claimClaimableBalance({ balanceId });
                    transaction.addOperation(claimBalance);
                    added = true;
                    trans.push(op);
                } else if (op.action == actions.merge) {
                    transaction.addOperation(sdk.Operation.accountMerge({
                        destination,
                    }));
                    added = true;
                    trans.push(op);
                } else if (op.action == actions.buyOffer) {
                    let buy_asset_issuer = addressGuard(op.buy_asset_issuer);
                    let sell_asset_issuer = addressGuard(op.sell_asset_issuer);
                    if (buy_asset_issuer && sell_asset_issuer) {
                        let buy_asset = GAS;
                        if (op.buy_asset_code != K.gas) {
                            buy_asset = new sdk.Asset(op.buy_asset_code, buy_asset_issuer);
                        };
                        let sell_asset = GAS;
                        if (op.sell_asset_code != K.gas) {
                            sell_asset = new sdk.Asset(op.sell_asset_code, sell_asset_issuer);
                        };
                        transaction.addOperation(sdk.Operation.manageBuyOffer({
                            selling: sell_asset,
                            buying: buy_asset,
                            buyAmount: amount.toString(),
                            price: op.price.toString(),
                            offerId: op.xid,
                        }));
                        added = true;
                        trans.push(op);
                    };
                } else if (op.action == actions.sellOffer) {
                    let buy_asset_issuer = addressGuard(op.buy_asset_issuer);
                    let sell_asset_issuer = addressGuard(op.sell_asset_issuer);
                    if (buy_asset_issuer && sell_asset_issuer) {
                        let buy_asset = GAS;
                        if (op.buy_asset_code != K.gas) {
                            buy_asset = new sdk.Asset(op.buy_asset_code, buy_asset_issuer);
                        };
                        let sell_asset = GAS;
                        if (op.sell_asset_code != K.gas) {
                            sell_asset = new sdk.Asset(op.sell_asset_code, sell_asset_issuer);
                        };
                        transaction.addOperation(sdk.Operation.manageSellOffer({
                            selling: sell_asset,
                            buying: buy_asset,
                            amount: amount.toString(),
                            price: op.price.toString(),
                            offerId: op.xid,
                        }));
                        added = true;
                        trans.push(op);
                    };
                };
            } catch (e) {
                const message = e.message;
                K.error(message);
                return message;
            };
        });
        let fullset = false;
        if (completeSet) {
            if (ops.length == trans.length) {
                fullset = true;
            };
        } else {
            fullset = true;
        };
        if (added && fullset) {
            const tx = transaction.build();
            signagures.forEach((s) => {
                tx.sign(s);
            });
            try {
                await server.submitTransaction(tx);
                K.print("Success");
                return true;
            } catch (e) {
                K.error(e.message);
                const message = e.response.data.extras.result_codes;
                K.error(message);
                return message;
            };
        } else {
            if (!completeSet) {
                throw new Error('No operations can be added.');
            } else {
                throw new Error('Added transactions were not complete.');
            };
        };
    } catch (e) {
        err = e.message;
        K.error(err);
        return err;
    };
};

export const addressGuard = (addr) => {
    let xid = addr;
    let valid = false;
    try {
        sdk.Keypair.fromPublicKey(
            xid,
        );
        valid = true;
    } catch {
        // K.error("not xid")
    };

    try {
        const zid = sdkEncoder.zidToxid(addr)
        sdk.Keypair.fromPublicKey(
            zid,
        );
        xid = zid;
        valid = true;
    } catch {
        // K.error("not zid")
    };

    try {
        let seed = addr;
        sdk.Keypair.fromSecret(
            seed,
        );
        xid = seed;
        valid = true;
    } catch {
        // K.error("not seed")
    };

    try {
        const zeed = sdkEncoder.zidToxid(addr, isSeed = true);
        sdk.Keypair.fromSecret(
            zeed,
        );
        xid = zeed;
        valid = true;
    } catch {
        // K.error("not zeed")
    };

    if (addr == K.native) {
        valid = true;
    };

    if (valid) {
        return xid;
    } else {
        return null;
    };

};