const axios = require("axios");
import { edCoder, sdkEncoder } from "../composables/cipherHandler";
import * as K from "../constants/constants";
import * as SH from "../secrets/sdkHandler";
import { trustLimit, nftmanager_zid, bookkeeper_zid, auctioneer_zid } from "../composables/sdkOperations"
import { dataLink } from "../secrets/secrets";
import { getCidLinks, flashThumbnail } from "../composables/handlers";
const CID = require('cids');

const sdk = SH.sdk;

export const getMuxedId = async(opUrl) => {
    let muxedId = "f01701220";
    const opsRes = await axios(opUrl);
    const opResJson = opsRes.data;
    const opRec = opResJson._embedded.records;
    opRec.forEach(async(e) => {
        if (e.transaction_successful) {
            if (e.to_muxed) {
                muxedId = "f" + ("000" + e.to_muxed_id).slice(-8, );
            };
        };
    });
    return muxedId;
};

export const readOffer = async(zid = nftmanager_zid, forceReturn = false, limit = 50) => {
    const xid = sdkEncoder.zidToxid(zid);
    const link = SH.link;
    let url = `${link}/accounts/${xid}/offers?limit=${limit}`;
    try {
        let response = await axios.get(url);
        if (response.data) {
            let responseJson = await response.data._embedded.records;
            responseJson = responseJson.filter((offer) => {
                if (
                    offer.buying.asset_type != K.native &&
                    offer.selling.asset_type != K.native
                ) {
                    return true;
                } else {
                    return false;
                };
            });
            let offers;
            if (!forceReturn) {
                offers = await Promise.all(responseJson.map(async(offer) => {
                    if (offer.buying.asset_code == K.asset || zid == bookkeeper_zid || zid == auctioneer_zid) {
                        try {
                            const nftMeta = await readTrans(offer.selling.asset_issuer, offer.selling.asset_code);
                            offer['memo'] = nftMeta.memo;
                            offer['meta'] = nftMeta.meta;
                            offer['thumbnail'] = nftMeta.thumbnail;
                        } catch {
                            offer = null;
                        };
                        return offer;
                    };
                }));
            } else {
                offers = responseJson
            };
            offers = offers.filter(offer => {
                if (offer) {
                    return true
                } else {
                    return false
                }
            });
            K.print(offers);
            return offers;
        }
    } catch (err) {
        K.error('cannot find data');
        return null;
    };
};


export const readTrans = async(xid, asset_code, link = SH.link, limit = 1) => {
    const cursor = edCoder.decode(asset_code);
    const url = `${link}/accounts/${xid}/transactions?cursor=${cursor}&order=asc&limit=${limit}&include_failed=false`;
    let response = await axios.get(url);
    let responseJson = response.data;
    let records = responseJson._embedded.records;
    if (sdkEncoder.xidTozid(records[0].fee_account) == nftmanager_zid ||
        sdkEncoder.xidTozid(records[0].fee_account) == auctioneer_zid) {
        const muxedId = await getMuxedId(records[0]._links.operations.href.split("{?cursor,limit,order}")[0]);
        let memo;
        let ipfsUrl;
        try {
            memo = new CID(muxedId + Buffer.from(records[0].memo, 'base64').toString('hex')).toV1().toString('base32');
            memo = new CID(memo).toV0().toString();
            ipfsUrl = getCidLinks(memo, true).link1;
        } catch {
            memo = new CID(muxedId + Buffer.from(records[0].memo, 'base64').toString('hex')).toV1().toString('base32');
            ipfsUrl = getCidLinks(memo).link1;
        };

        const preUrl = `${dataLink}tokens/${memo}.json`;

        let fetchResponse;
        let meta;
        try {
            fetchResponse = await axios.get(preUrl);
            meta = fetchResponse.data;
        } catch (err) {
            fetchResponse = await axios.get(ipfsUrl);
            meta = fetchResponse.data;
        };
        const thumbnail = await flashThumbnail(meta.properties.meta.thumbnail);
        return { memo, meta, thumbnail }
    } else {
        return
    };
};