<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Collectables 收藏</h4>
    <div v-if="!isLoaded" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>

    <div v-else>
      <div v-if="assets.length > 0">
        <q-pull-to-refresh @refresh="refresh">
          <q-list>
            <q-item v-for="a in assets" :key="sanitize(a.asset_code)">
              <div class="q-px-none q-pb-sm row items-start q-gutter-md">
                <q-card class="my-card">
                  <q-img :src="sanitize(a.thumbnail)" @click="openCard(a)">
                    <div class="absolute-top text-subtitle2 text-right">
                      <span
                        >Collectables 收藏
                        <span v-if="a.meta.properties.meta.lot != '0'"
                          >(NFT)</span
                        ></span
                      >
                      <span v-if="a.count > 1"> x{{ a.count }} </span>
                    </div>
                  </q-img>

                  <q-card-section>
                    <div
                      class="text-h6"
                      :class="{
                        forcewrap: wrapTitle(sanitize(a.meta.name)),
                      }"
                    >
                      {{ sanitize(a.meta.name) }}
                    </div>
                    <div
                      v-if="a.meta.properties.meta.lot != '0'"
                      class="text-subtitle2"
                    >
                      LOT
                      {{
                        a.count > 1
                          ? `(${a.lots.toString()})`
                          : a.meta.properties.meta.lot
                      }}
                      of
                      {{ sanitize(a.meta.properties.meta.limit) }}
                    </div>
                    <div v-else>
                      <div>
                        Owned 擁有
                        {{ (a.amount / trustLimit.MIN).toLocaleString() }} /
                        {{ a.meta.properties.meta.limit }}
                      </div>
                    </div>
                  </q-card-section>

                  <q-card-section class="q-pt-none prewrap">
                    {{ sanitize(a.meta.description) }}
                  </q-card-section>
                  <AppButton text="Open 開" :action="openCard" :args="a" />
                </q-card>
              </div>
            </q-item>
          </q-list>
        </q-pull-to-refresh>
      </div>
      <div v-else>
        <p class="introText">
          Welcome to the Collectables page! You can trade Tokens from the
          exchange.
        </p>
        <p>歡迎嚟到收藏。請先交換良心代幣</p>
        <AppButton text="Go 去" :action="toTokens" />
      </div>
    </div>
  </div>
  <q-dialog v-model="card" persistent>
    <q-card class="my-card">
      <q-card-section style="max-height: 100vh" class="q-pt-sm scroll">
        <a :href="getCidLinks(selected.meta.image).link1" target="_blank">
          <q-img :src="selected.thumbnail">
            <div
              class="absolute-top text-subtitle2 text-center"
              :class="{ forcewrap: wrapTitle(selected.meta.name) }"
            >
              {{ selected.meta.name }}
            </div>
          </q-img>
        </a>

        <div
          v-if="selected.meta.properties.meta.lot != '0'"
          class="text-subtitle2"
        >
          LOT
          {{
            selected.count > 1
              ? `(${selected.lots.toString()})`
              : selected.meta.properties.meta.lot
          }}
          of
          {{ selected.meta.properties.meta.limit }}
        </div>
        <div v-else>
          <div>
            Owned 擁有
            {{ (selected.amount / trustLimit.MIN).toLocaleString() }} /
            {{ selected.meta.properties.meta.limit }}
          </div>
        </div>

        <div
          class="text-subtitle1"
          :class="{ forcewrap: wrapTitle(selected.meta.description) }"
        >
          <!-- {{ selected.meta.description }} -->
        </div>

        <div class="text-caption text-grey forcewrap">
          {{ selected.meta.image }}
        </div>

        <q-separator />
        <div>
          <q-item-label header> </q-item-label>
        </div>
        <div class="text-subtitle1">
          <!-- <q-item-label header> </q-item-label> -->
        </div>

        <div
          v-if="!isPaying"
          class="row q-mb-md q-pb-md justify-between q-px-lg"
        >
          <a :href="getCidLinks(selected.meta.image).link1" target="_blank">
            <AppButton text="Appreciate 欣賞" :action="() => {}" />
          </a>
          <q-btn icon="close" flat rounded dense v-close-popup />
        </div>
        <div v-else style="text-align: center" class="q-pa-md q-mb-md">
          <q-spinner-cube color="primary" size="2.5rem" />
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import getAccount from "../composables/readAccount";
import * as K from "../constants/constants";
import { sdkEncoder, Decrypt } from "../composables/cipherHandler";
import {
  getCidLinks,
  storage,
  readWallet,
  getPWADisplayMode,
  countDecimals,
  singularList,
  nftTagsFilter,
} from "../composables/handlers";
import { ReadClaimable } from "../composables/readClaimable";
import { dataLink } from "../secrets/secrets";
import { readTrans } from "../composables/readOffer";
import { trustLimit, auctioneer_zid } from "../composables/sdkOperations";
const sanitize = require("htmlsanitize");

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const isLoaded = ref(false);
    const numberOfEntries = ref(0);
    const assets = ref([]);
    const hasNetwork = ref(true);
    const needRestore = ref(true);
    const card = ref(false);
    const selected = ref({});
    const isPaying = ref(false);
    const { accounts, fetchAccount, records } = getAccount();

    const loadData = async () => {
      numberOfEntries.value = window.history.length;
      if (getPWADisplayMode() == "standalone" && numberOfEntries.value >= 2) {
        router.go(numberOfEntries.value - 1);
      }
      const wallet = await readWallet();
      const key = wallet.pack.key;

      if (window.navigator.onLine) {
        try {
          const hasResult = await fetchAccount();

          if (hasResult) {
            assets.value = (
              await JSON.parse(
                Decrypt(await storage.get("balances"), wallet.pack.key)
              )
            ).filter((account) => {
              return (
                account.nft &&
                Number(account.amount) > 0 &&
                auctioneer_zid != account.asset_issuer
              );
            });
          }
          try {
            assets.value = await Promise.all(
              assets.value.map(async (e) => {
                const resTrans = await readTrans(
                  sdkEncoder.zidToxid(e.asset_issuer),
                  e.asset_code
                );
                e["meta"] = resTrans.meta;
                e["memo"] = resTrans.memo;
                e["thumbnail"] = resTrans.thumbnail;
                return e;
              })
            );
          } catch {
            assets.value = [];
          }

          assets.value = nftTagsFilter(assets.value);
        } catch (err) {
          K.error(err.message);
        }
      } else {
        hasNetwork.value = false;
        const colContent = Decrypt(await storage.get("colContent"), key);
        try {
          assets.value = await JSON.parse(colContent);
        } catch {
          assets.value = [];
        }
      }
      assets.value.sort((a, b) => {
        if (a.meta.name < b.meta.name) {
          return -1;
        }
        if (a.meta.name > b.meta.name) {
          return 1;
        }
        if (a.meta.image < b.meta.image) {
          return -1;
        }
        if (a.meta.image > b.meta.image) {
          return 1;
        }
        if (
          parseFloat(a.meta.properties.meta.lot) <
          parseFloat(b.meta.properties.meta.lot)
        ) {
          return -1;
        }
        if (
          parseFloat(a.meta.properties.meta.lot) >
          parseFloat(b.meta.properties.meta.lot)
        ) {
          return 1;
        }
      });
      let unique = singularList(assets.value);
      assets.value = unique;
      K.print(assets.value);
      isLoaded.value = true;
    };

    loadData();

    const openCard = (item) => {
      selected.value = item;
      card.value = true;
    };

    const toTokens = () => {
      router.replace({ name: "Tokens" });
    };

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    return {
      toTokens,
      getCidLinks,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
      assets,
      isLoaded,
      wrapTitle,
      trustLimit,
      sanitize,
      openCard,
      needRestore,
      card,
      selected,
      isPaying,
      countDecimals,
    };
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

