<template>
  <div v-if="!isLoaded" style="text-align: center">
    <q-spinner-grid color="primary" size="2.5rem" />
  </div>
  <div v-else>
    <div class="textwrap q-px-md" v-html="markdownToHtml"></div>
    <div v-if="markdown == '' || !hasNetwork">
      <p>Nothing to see 冇嘢睇</p>
    </div>

    <div v-if="signable != ''">
      <div v-if="signed">
        <div class="q-px-md">
          <p>Thank you for signing!</p>
          <p>多謝支持！</p>
        </div>
        <AppButton text="保育鏗鏘集" :action="goIPFS" />
      </div>
      <div v-else>
        <div v-if="needRestore">
          <div class="q-px-md">
            <q-input
              bottom-slots
              v-model="name"
              label="Sign 簽署"
              maxlength="100"
              :rules="[
                (val) => val.length >= 1 || 'Input at least 1 character',
              ]"
              style="font-size: 1.25rem"
            >
            </q-input>
          </div>
          <div>
            <AppButton
              text="Sign 簽署"
              :action="signHandler"
              :disabled="name == ''"
            />
          </div>
        </div>
        <div v-else>
          <AppButton
            text="Sign 簽署"
            :action="signHandler"
            :disabled="name == ''"
          />
        </div>
      </div>
    </div>

    <div class="q-mt-md">
      <AppButton text="Back 返去" :action="goBack" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import AppButton from "../components/AppButton.vue";
const axios = require("axios");
import marked from "marked";
import { postsLink } from "../constants/appLinks";
import { readWallet, storage } from "../composables/handlers";
import { Decrypt, Encrypt } from "../composables/cipherHandler";
import { assetDefinition } from "../secrets/secrets";
import { cb_email } from "../secrets/secrets";
import * as K from "../constants/constants";

export const postCode = assetDefinition.postCode;

export default {
  components: { AppButton },
  emits: ["reload"],
  props: ["id", "back"],
  setup(props) {
    const router = useRouter();
    const route = useRoute();
    const hasNetwork = ref(true);
    const isLoaded = ref(false);
    const markdown = ref("");
    const needRestore = ref(true);
    const name = ref("");
    const signable = ref("");
    const signed = ref(false);
    const key = ref(K.issuer);

    const goBack = async () => {
      let target = { name: "Petitions" };
      try {
        target = await JSON.parse(props.back);
      } catch (err) {
        target = { name: "Petitions" };
      }
      router.replace(target);
    };

    const goIPFS = async () => {
      await router.replace('/thecommonsense#header');
    };

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const signHandler = async () => {
      const callUrl = `${signable.value}${name.value}`;
      let petitionList = [];
      const petitionSigned = { id: props.id };
      try {
        petitionList = JSON.parse(
          Decrypt(await storage.get("petitions"), key.value)
        );
        petitionList.unshift(petitionSigned);
      } catch {
        petitionList.unshift(petitionSigned);
      }
      try {
        const config = {
          mode: "no-cors",
        };
        await fetch(callUrl, config);
      } catch {}
      const enc = await Encrypt(petitionList, key.value);
      await storage.set("petitions", enc);
      signed.value = true;
    };

    const loadData = async () => {
      const wallet = await readWallet();
      if (wallet.secure) {
        if (wallet.secure.mnemonic) {
          needRestore.value = false;
          name.value = wallet.disclose.zid + cb_email();
          key.value = wallet.pack.key;
        } else {
          needRestore.value = true;
        }
      }
      let url = `${postsLink.split("index")[0]}posts/${route.params.id}.md`;

      if (window.navigator.onLine) {
        try {
          let resp = await axios.get(url);
          markdown.value = resp.data;
          let mdv = markdown.value
            .split('src="#postto"')[1]
            .split("name=")[1]
            .replace("/>", "");
          let dec = JSON.parse(Decrypt(mdv, postCode));
          if (dec.m) {
            signable.value = dec.m;

            try {
              const petitionList = JSON.parse(
                Decrypt(await storage.get("petitions"), key.value)
              );
              let flist = petitionList.filter((p) => {
                return p.id == props.id;
              });
              if (flist.length > 0) {
                signed.value = true;
              }
            } catch {
              signed.value = false;
            }
          }
        } catch {
          markdown.value = "";
          signable.value = "";
        }
      } else {
        hasNetwork.value = false;
      }
      isLoaded.value = true;
    };

    loadData();

    return {
      goBack,
      goIPFS,
      toAccount,
      hasNetwork,
      isLoaded,
      markdown,
      needRestore,
      name,
      signHandler,
      signable,
      signed,
    };
  },
  computed: {
    markdownToHtml() {
      return marked(this.markdown);
    },
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1.25rem;
}
p {
  white-space: pre-wrap;
}
.textwrap {
  max-width: 100vw;
}
.forcewrap {
  word-break: break-all;
}
</style>