<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Tokens 良心代幣</h4>
    <div v-if="!isLoaded" class="q-pb-md" style="text-align: center">
      <q-spinner-grid color="primary" size="2.5rem" />
    </div>
    <div v-else>
      <div v-if="assets.length > 0">
        <q-pull-to-refresh @refresh="refresh">
          <q-list>
            <q-item
              v-for="a in assets"
              :key="sanitize(a.meta.properties.meta.asset_code)"
            >
              <div class="q-px-none q-pb-sm row items-start q-gutter-md">
                <q-card class="my-card">
                  <q-img
                    :src="sanitize(a.thumbnail)"
                    @click="
                      () => {
                        openCard(a);
                      }
                    "
                  >
                    <div class="absolute-top text-subtitle2 text-right">
                      Tokens 良心代幣
                      <span v-if="a.meta.properties.meta.lot != '0'"
                        >(NFT)</span
                      >
                    </div>
                  </q-img>

                  <q-card-section>
                    <div
                      class="text-h6"
                      :class="{
                        forcewrap: wrapTitle(sanitize(a.meta.name)),
                      }"
                    >
                      {{ sanitize(a.meta.name) }}
                    </div>
                    <div
                      v-if="a.meta.properties.meta.lot != '0'"
                      class="text-subtitle2"
                    >
                      LOT
                      {{ sanitize(a.meta.properties.meta.lot) }}
                      of
                      {{ sanitize(a.meta.properties.meta.limit) }}
                    </div>
                    <div v-else>
                      <div>
                        Remaining 淨返
                        {{ (a.amount / trustLimit.MIN).toLocaleString() }}
                      </div>
                    </div>
                  </q-card-section>

                  <q-card-section class="q-pt-none prewrap">
                    {{ sanitize(a.meta.description) }}
                  </q-card-section>
                  <AppButton text="Open 開" :action="openCard" :args="a" />
                </q-card>
              </div>
            </q-item>
          </q-list>
        </q-pull-to-refresh>
      </div>
      <div v-else>
        <div v-if="!hasNetwork">
          <p>No network, please retry</p>
          <p>冇網絡 請再試</p>
        </div>
        <div v-else>
          <p>Welcome to the Tokens page!</p>
          <p>歡迎嚟到良心代幣！</p>
          <p>All sold out, please try again later!</p>
          <p>全部賣清光，明天請早！</p>
          <!-- <p>Upgrading to ERC1155 standard, be back soon!!</p>
          <p>ERC1155升級進行中，請等等！</p> -->
        </div>
        <AppButton text="Back 返去" :action="toAccount" />
      </div>
    </div>
  </div>
  <q-dialog :persistent="isPaying" v-model="card">
    <q-card class="my-card">
      <q-card-section
        class="q-pt-sm scroll"
        :class="{
          cardHeight:
            selected.meta.properties.meta.lot != '0' || windowWidth >= 550,
        }"
      >
        <!-- <p> class selection </p> -->
        <div class="justify-between">
          <div>
            <a :href="getCidLinks(selected.meta.image).link1" target="_blank">
              <q-img :src="selected.thumbnail">
                <!-- <div
              class="absolute-top text-subtitle2 text-center"
              :class="{ forcewrap: wrapTitle(selected.meta.name) }"
            >
              {{ selected.meta.name }}
            </div> -->
              </q-img>
            </a>
          </div>

          <div
            class="text-h6"
            :class="{
              forcewrap: wrapTitle(sanitize(selected.meta.name)),
            }"
          >
            {{ sanitize(selected.meta.name) }}
          </div>
          <div
            v-if="selected.meta.properties.meta.lot != '0'"
            class="text-subtitle2"
          >
            LOT {{ selected.meta.properties.meta.lot }} of
            {{ selected.meta.properties.meta.limit }}
          </div>
          <div v-else>
            <div>
              Remaining 淨返
              {{ (selected.amount / trustLimit.MIN).toLocaleString() }}
            </div>
          </div>

          <div
            class="text-subtitle1"
            :class="{ forcewrap: wrapTitle(selected.meta.description) }"
          >
            <!-- {{ selected.meta.description }} -->
          </div>

          <div class="text-caption text-grey forcewrap">
            {{ selected.meta.image }}
          </div>

          <q-separator />
          <div v-if="!checkBidded(selected.selling.asset_code)">
            <q-item-label header>
              <span
                v-if="key != K.asset_alias"
                class="infoText"
                @click="
                  () => {
                    if (isValid) {
                      showSlider = !showSlider;
                      slideAmt = Number(slideAmt);
                      if (!showSlider) {
                        maxBid = assetAmount;
                      } else {
                        maxBid = 100;
                        slideAmt = Math.min(100, slideAmt);
                      }
                    }
                  }
                "
              >
                Bid 競投
                <!-- {{
                Number(slideAmt).toLocaleString(undefined, {
                  minimumFractionDigits: countDecimals(slideAmt),
                })
              }} -->
                {{
                  selected.meta.properties.meta.lot != "0"
                    ? Number(streamPrice).toFixed(0)
                    : (selected.price * trustLimit.MIN).toFixed(0)
                }}
                {{ K.asset_alias }}
              </span>
              <div
                class="q-pt-sm"
                v-if="Number(myAuctionBid).toFixed(0) != '0'"
                @click="cancelBid(selected)"
              >
                Your bid {{ Number(myAuctionBid).toFixed(0) }}
                {{ K.asset_alias }}
                <span v-if="Number(streamPrice) - 10 == Number(myAuctionBid)">
                  is one of the highest bid</span
                >
                <span v-else>has been outbided</span> <br />
                你嘅競投價 {{ Number(myAuctionBid).toFixed(0) }}
                {{ K.asset_alias }}
                <span v-if="Number(streamPrice) - 10 == Number(myAuctionBid)">
                  係最高競投價之一
                </span>
                <span v-else>已被超越</span>
              </div>
              <!-- <span class="infoText" v-if="!showSlider">
              <q-input
                outlined
                v-model="slideAmt"
                type="number"
                :step="10"
                :min="
                  selected.meta.properties.meta.lot != '0'
                    ? Number(streamPrice).toFixed(0)
                    : (selected.price * trustLimit.MIN).toFixed(0)
                "
                :max="maxBid"
                :error="!isValid"
              />
            </span> -->
            </q-item-label>
            <div
              v-if="
                selected.meta.properties.meta.lot != '0' &&
                showSlider &&
                key != K.asset_alias
              "
            >
              <q-item dense>
                <q-item-section avatar>
                  <q-icon name="fas fa-bomb" />
                </q-item-section>
                <q-item-section>
                  <q-slider
                    color="primary"
                    v-model="slideAmt"
                    :step="10"
                    :min="Number(streamPrice)"
                    :max="maxBid"
                  />
                  {{ slideAmt }}
                </q-item-section>
              </q-item>
            </div>
          </div>

          <div
            v-if="!isPaying"
            class="row q-mb-md q-pb-md justify-between q-px-lg"
          >
            <AppButton
              text="Bid 競投"
              class="q-mb-sm"
              :action="bidHandler"
              :args="selected"
              :disabled="
                checkBidded(selected.selling.asset_code) ||
                needRestore ||
                !isValid ||
                assetAmount == 0
              "
            />

            <q-btn
              @click="
                () => {
                  //showSlider = true;
                  //maxBid = 100;
                  //slideAmt = Math.min(maxBid, slideAmt);
                  //slideAmt = Math.max(20, slideAmt);
                  //slideAmt = Math.trunc(slideAmt);
                }
              "
              icon="close"
              flat
              rounded
              dense
              v-close-popup
            />
          </div>
          <div v-else style="text-align: center" class="q-pa-md q-mb-md">
            <q-spinner-cube color="primary" size="2.5rem" />
          </div>
        </div>
      </q-card-section>
    </q-card>
  </q-dialog>
  <div v-if="!needRestore">
    <q-footer>
      <div
        class="row q-px-lg q-mt-sm justify-center btnStyle"
        style="max-width: 700px"
      >
        <div class="q-my-sm q-mb-md text-center">
          <q-btn outline rounded color="white" @click="toCollectables" no-caps>
            <!-- <div>
              <q-icon color="white" name="south_east" />
              <q-icon color="white" :name="iconSet[K.asset]" />
            </div> -->
            <span class="q-px-sm">Collectables 收藏</span>
          </q-btn>
        </div>
      </div>
    </q-footer>
  </div>
</template>

<script>
import { ref, computed } from "vue";
import { useRouter, useRoute } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { ReadClaimable } from "../composables/readClaimable";
import {
  nftissuer_zid,
  Operations,
  actions,
  issue_zid,
  nftmanager_zid,
  bookkeeper_zid,
  auctioneer_zid,
  trustLimit,
} from "../composables/sdkOperations";
import {
  getCidLinks,
  getPWADisplayMode,
  readWallet,
  storage,
  notifyHandler,
  confirmPin,
  countDecimals,
  singularList,
  nftTagsFilter,
  dialogHandler,
} from "../composables/handlers";
var axios = require("axios");
import { Encrypt, Decrypt, sdkEncoder } from "../composables/cipherHandler";
import * as K from "../constants/constants";
import { getClaimants, modes } from "../composables/getClaimants";
import { readOffer } from "../composables/readOffer";
import { readBalance } from "../composables/readBalance";
import * as SH from "../secrets/sdkHandler";
import getAccount from "../composables/readAccount";
const sanitize = require("htmlsanitize");

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const assets = ref([]);
    const numberOfEntries = ref(0);
    const router = useRouter();
    const isLoaded = ref(false);
    const needRestore = ref(true);
    const hasNetwork = ref(true);
    const isPaying = ref(false);
    const card = ref(false);
    const selected = ref({});
    const myBids = ref([]);
    const slideAmt = ref(20);
    const showSlider = ref(true);
    const maxBid = ref(100);
    const assetAmount = ref(0);
    const key = ref(K.asset_alias);
    const streamPrice = ref("0");
    const { accounts, fetchAccount, records } = getAccount();
    const myAuctionBid = ref("0");

    const toAccount = () => {
      router.replace({ name: "Account" });
    };

    const toCollectables = () => {
      router.replace({ name: "Collectables" });
    };

    const cancelBid = async (code) => {
      dialogHandler({
        title: "Cancel Bid 取消競投?",
        message:
          "Are you sure you want to cancel your bid?<br>你係唔係確認取消競投？",
        persistent: true,
        html: true,
        ok: {
          label: "Confirm 確認",
          style: { "text-transform": "none" },
          color: "red",
        },
        cancel: {
          label: "Cancel 取消",
          style: { "text-transform": "none" },
        },
      }).onOk(async () => {
        const wallet = await readWallet();
        let myOffers = await readOffer(wallet.disclose.zid, true);
        myOffers = myOffers.filter((offer) => {
          if (
            offer.buying.asset_code == code.selling.asset_code &&
            offer.buying.asset_issuer == code.selling.asset_issuer
          ) {
            return true;
          }
        });
        const trans = [
          {
            action: actions.sellOffer,
            xid: myOffers[0].id,
            buy_asset_code: myOffers[0].buying.asset_code,
            buy_asset_issuer: myOffers[0].buying.asset_issuer,
            sell_asset_code: myOffers[0].selling.asset_code,
            sell_asset_issuer: myOffers[0].selling.asset_issuer,
            amount: "0",
            price: "1",
          },
        ];
        await Operations(trans, `remove offer ${myOffers[0].id}`);
        await openCard(code);
      });
    };

    const loadDataNft = async () => {
      numberOfEntries.value = window.history.length;
      if (getPWADisplayMode() == "standalone" && numberOfEntries.value >= 2) {
        router.go(numberOfEntries.value - 1);
      }

      if (K.nftOpen) {
        const wallet = await readWallet();

        try {
          key.value = wallet.pack.key;
        } catch {
          key.value = K.asset_alias;
        }

        if (wallet.secure) {
          if (wallet.secure.mnemonic) {
            needRestore.value = false;
          }
        }

        if (window.navigator.onLine) {
          // isLoaded.value = false;
          if (key.value != K.asset_alias) {
            try {
              assetAmount.value = await readBalance(wallet.disclose.zid, [
                { asset_code: K.asset, asset_issuer: issue_zid },
              ]);
              assetAmount.value = Number(assetAmount.value[0].amount);
            } catch {
              assetAmount.value = 0;
            }
          }

          let claimable = await readOffer();
          let auctionOffers = await readOffer(auctioneer_zid);
          let bookOffers = await readOffer(bookkeeper_zid);
          bookOffers = bookOffers.filter((offer) => {
            const checker = auctionOffers.filter((a) => {
              if (a.memo == offer.memo) {
                return true;
              }
            });
            if (checker.length > 0) {
              return true;
            }
          });
          claimable = [...bookOffers, ...claimable];

          if (key.value != K.asset_alias && wallet.disclose.zid) {
            let getMyBids = await ReadClaimable("");
            myBids.value = getMyBids.filter((e) => {
              if (
                e.claimants.length == 2 &&
                sdkEncoder.xidTozid(e.claimants[1].destination) ==
                  wallet.disclose.zid &&
                sdkEncoder.xidTozid(e.sponsor) == wallet.disclose.zid &&
                e.asset.split(":")[0] == K.asset &&
                sdkEncoder.xidTozid(e.asset.split(":")[1]) == issue_zid
              ) {
                try {
                  const claimAfter = Date.parse(
                    new Date(e.claimants[1].predicate.not.abs_before)
                  );
                  K.print(
                    `${(claimAfter - Date.now()) / 1000 / 60} mins to claim`
                  );
                  if (Date.now() < claimAfter) {
                    return true;
                  } else {
                    return false;
                  }
                } catch {
                  return false;
                }
              }
            });
          }

          isLoaded.value = false;

          assets.value = claimable;
        } else {
          assets.value = [];
          hasNetwork.value = false;
        }
        assets.value = nftTagsFilter(assets.value);
        assets.value.sort((a, b) => {
          if (a.meta.name < b.meta.name) {
            return -1;
          }
          if (a.meta.name > b.meta.name) {
            return 1;
          }
          if (a.meta.image < b.meta.image) {
            return -1;
          }
          if (a.meta.image > b.meta.image) {
            return 1;
          }
          if (
            parseFloat(a.meta.properties.meta.lot) <
            parseFloat(b.meta.properties.meta.lot)
          ) {
            return -1;
          }
          if (
            parseFloat(a.meta.properties.meta.lot) >
            parseFloat(b.meta.properties.meta.lot)
          ) {
            return 1;
          }
        });
        let unique = singularList(assets.value);
        assets.value = unique;
        K.print(assets.value);
        isLoaded.value = true;
      } else {
        await storage.remove("nftContent");
        await storage.remove("nftIndex");
        isLoaded.value = true;
      }
    };

    const bidHandler = async (code) => {
      const wallet = await readWallet();
      await getStreamPrice(code);
      const passFunc = async () => {
        isPaying.value = true;
        const targetxid = sdkEncoder.zidToxid(nftmanager_zid);
        const sourcexid = sdkEncoder.zidToxid(wallet.disclose.zid);
        let trustAmount = trustLimit.MIN;
        let bidAmount = trustLimit.MIN;
        let price;
        if (code.meta.properties.meta.lot == "0") {
          trustAmount = (
            Number(code.meta.properties.meta.limit) * Number(trustLimit.MIN)
          ).toFixed(7);
        }
        if (sdkEncoder.xidTozid(code.selling.asset_issuer) == auctioneer_zid) {
          trustAmount = code.amount;
          bidAmount = code.amount;
          price = Math.max(Number(price), Number(slideAmt.value));
        } else {
          price = (Number(streamPrice.value) / Number(trustLimit.MIN)).toString()
        };
        let accountBalances = await fetchAccount();
        accountBalances = accountBalances.balances.filter((balance) => {
          if (
            balance.asset_code == code.selling.asset_code &&
            balance.asset_issuer ==
              sdkEncoder.xidTozid(code.selling.asset_issuer)
          ) {
            return true;
          }
        });
        let trans = [];
        try {
          if (accountBalances[0].limit == trustAmount) {
            let myOffers = await readOffer(wallet.disclose.zid, true);
            myOffers = myOffers.filter((offer) => {
              if (
                offer.buying.asset_code == code.selling.asset_code &&
                offer.buying.asset_issuer == code.selling.asset_issuer
              ) {
                return true;
              }
            });
            let offerId = "0";
            if (myOffers.length > 0) {
              offerId = myOffers[0].id;
            }
            trans = [
              {
                action: actions.buyOffer,
                xid: offerId,
                buy_asset_code: code.selling.asset_code,
                buy_asset_issuer: code.selling.asset_issuer,
                sell_asset_code: K.asset,
                sell_asset_issuer: sdkEncoder.zidToxid(issue_zid),
                amount: bidAmount,
                price: price.toString(),
              },
            ];
          }
        } catch {
          trans = [
            {
              action: actions.trust,
              xid: code.selling.asset_issuer,
              asset_code: code.selling.asset_code,
              amount: trustAmount,
            },
            {
              action: actions.buyOffer,
              xid: "0",
              buy_asset_code: code.selling.asset_code,
              buy_asset_issuer: code.selling.asset_issuer,
              sell_asset_code: K.asset,
              sell_asset_issuer: sdkEncoder.zidToxid(issue_zid),
              amount: bidAmount,
              price: streamPrice.value.toString(),
            },
          ];
        }
        const res = await Operations(trans, code.selling.asset_code);
        // const res = false;

        if (res == true) {
          const balances = await readBalance(wallet.disclose.zid, [
            {
              asset_code: code.selling.asset_code,
              asset_issuer: code.selling.asset_issuer,
            },
          ]);
          const collected = balances.filter((balance) => {
            if (balance.asset_code == code.selling.asset_code) {
              if (balance.amount > 0) {
                return true;
              }
            }
            return false;
          });
          const remain =
            parseFloat(slideAmt.value) -
            parseFloat(code.price) * parseFloat(trustLimit.MIN);
          if (remain > 0 && collected.length > 0) {
            let tran = [
              {
                action: actions.payment,
                xid: bookkeeper_zid,
                asset_code: K.asset,
                amount: remain.toString(),
              },
            ];
            await Operations(tran, code.selling.asset_code);
          }
          await loadDataNft();
          notifyHandler({
            message: "Bid Submitted!",
            color: "green",
          });
          card.value = false;
        } else {
          notifyHandler({
            message: "Bid Failed!",
            color: "negative",
          });
        }
        isPaying.value = false;
      };
      await confirmPin(passFunc);
    };

    const getStreamPrice = async (code) => {
      if (
        code.meta.properties.meta.lot != "0" &&
        code.amount > trustLimit.MIN
      ) {
        let url = `${SH.link}/offers?selling_asset_type=credit_alphanum12&selling_asset_code=${code.buying.asset_code}&selling_asset_issuer=${code.buying.asset_issuer}&buying_asset_type=credit_alphanum12&buying_asset_code=${code.selling.asset_code}&buying_asset_issuer=${code.selling.asset_issuer}&order=desc&limit=200`;
        let response = await axios.get(url);
        let responseJson = response.data;

        if (responseJson._embedded.records) {
          if (responseJson._embedded.records.length == 0) {
            streamPrice.value = "10";
          } else {
            // sort res by amount in desc order
            responseJson._embedded.records =
              responseJson._embedded.records.sort((a, b) => {
                if (Number(a.amount) > Number(b.amount)) {
                  return -1;
                }
                if (Number(a.amount) < Number(b.amount)) {
                  return 1;
                }
              });
            streamPrice.value = (
              Number(responseJson._embedded.records[0].amount) + 10
            ).toFixed(0);
          }
        }
      } else {
        streamPrice.value = (
          Number(code.price) * Number(trustLimit.MIN)
        ).toFixed(0);
      }
    };

    const openCard = async (item) => {
      try {
        selected.value = item;
        card.value = true;
        const code = selected.value;
        await getStreamPrice(code);
        const wallet = await readWallet();
        let myOffers = await readOffer(wallet.disclose.zid, true);
        myOffers = myOffers.filter((offer) => {
          if (
            offer.buying.asset_code == code.selling.asset_code &&
            offer.buying.asset_issuer == code.selling.asset_issuer
          ) {
            return true;
          }
        });
        if (myOffers.length > 0) {
          myAuctionBid.value = myOffers[0].amount;
        } else {
          myAuctionBid.value = "0";
        }
        // fetch
        let accountBalances = await fetchAccount();

        const assetAmount = accountBalances.balances.filter((balance) => {
          if (
            balance.asset_code == K.asset &&
            balance.asset_issuer == K.issuer
          ) {
            return true;
          }
        });
        maxBid.value =
          Number(
            Math.floor(
              (Number(assetAmount[0].amount) -
                Number(assetAmount[0].selling_liabilities)) /
                10
            )
          ) * 10;
        slideAmt.value = Number(streamPrice.value);
      } catch {}
    };

    loadDataNft();

    const wrapTitle = (title) => {
      const tmpArr = title.split(" ");
      const arrLen = tmpArr.map((e) => {
        return e.length;
      });
      const maxLength = Math.max(...arrLen);
      if (maxLength > 20) {
        return true;
      } else {
        return false;
      }
    };

    const checkBidded = (asset_code) => {
      if (key.value != K.asset_alias) {
        let bidder = myBids.value.filter((e) => {
          return e.memo == asset_code;
        });
        if (bidder.length > 0) {
          return true;
        } else {
          return false;
        }
      } else {
        return true;
      }
    };

    return {
      assets,
      toAccount,
      toCollectables,
      getCidLinks,
      async refresh(done) {
        setTimeout(() => {
          loadDataNft();
          done();
        }, 2500);
      },
      isLoaded,
      hasNetwork,
      needRestore,
      bidHandler,
      isPaying,
      wrapTitle,
      card,
      openCard,
      selected,
      myBids,
      checkBidded,
      slideAmt,
      K,
      sanitize,
      isValid: computed(
        () =>
          slideAmt.value >= Number(streamPrice.value) &&
          slideAmt.value <= maxBid.value &&
          assetAmount.value > 10 &&
          countDecimals(slideAmt.value) == 0
      ),
      showSlider,
      maxBid,
      assetAmount,
      countDecimals,
      key,
      trustLimit,
      streamPrice,
      myAuctionBid,
      cancelBid,
    };
  },
  data: function () {
    return {
      windowWidth: 0,
      windowHeight: 0,
    };
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.getWindowWidth);
      window.addEventListener("resize", this.getWindowHeight);
      this.getWindowWidth();
      this.getWindowHeight();
    });
  },
  methods: {
    getWindowWidth(event) {
      this.windowWidth = document.documentElement.clientWidth;
    },
    getWindowHeight(event) {
      this.windowHeight = document.documentElement.clientHeight;
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.getWindowWidth);
    window.removeEventListener("resize", this.getWindowHeight);
  },
  beforeRouteLeave: async function (to, from, next) {
    if (this.isPaying) {
      next(false);
    } else {
      next();
    }
  },
};
</script>

