<template>
  <div>
    <h4 class="q-mt-none q-mb-md text-weight-bold">Hash</h4>
    <q-input
      bottom-slots
      v-model="message"
      label="Message"
      style="font-size: 1.25rem"
    >
      <template v-slot:append>
        <q-icon
          name="close"
          @click="
            () => {
              message = '';
              hashText = '';
            }
          "
          class="cursor-pointer"
        />
      </template>
    </q-input>
    <div class="forcewrap" :class="{ redText: toggleChange() }">
      {{ hashText }}
    </div>
    <AppButton text="Get Hash" :action="getHash" />
    <AppButton text="Back" :action="toAccount" />
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "../components/AppButton.vue";
import { makeHash } from "../composables/cipherHandler";

export default {
  components: { AppButton },
  emits: ["reload"],
  setup() {
    const router = useRouter();
    const message = ref("");
    const hashText = ref("");
    const endloop = ref(false);
    const changed = ref(false);
    const textHashed = ref("");

    const toggleChange = () => {
      if (textHashed.value == message.value) {
        return false;
      } else {
        return true;
      }
    };

    const toAccount = () => {
      endloop.value = true;
      router.replace({ name: "Account" });
    };

    const getHash = async () => {
      if (message.value != "") {
        const cid = await makeHash.hash(message.value);
        hashText.value = cid;
        changed.value = false;
        textHashed.value = message.value;
        return cid;
      } else {
        hashText.value = "";
      }
    };

    return {
      message,
      getHash,
      hashText,
      toAccount,
      changed,
      toggleChange,
    };
  },
};
</script>

