<template>
  <div id="reader" class="fullScreen"></div>
</template>

<script>
import { Html5Qrcode } from "html5-qrcode";
import { clearCamera } from "../composables/handlers";
import { useRouter, useRoute } from "vue-router";
import * as K from "../constants/constants";
import { ref } from "vue";
const { Base64 } = require("js-base64");

export default {
  setup(props, context) {
    const router = useRouter();
    const route = useRoute();
    const rtName = route.name;
    const loaded = ref(false);

    // This method will trigger user permissions
    Html5Qrcode.getCameras()
      .then((devices) => {
        /**
         * devices would be an array of objects of type:
         * { id: "id", label: "label" }
         */
        if (devices && devices.length) {
          let cameraId = devices[devices.length - 1].id;
          if (devices.length > 1) {
            cameraId = { facingMode: { exact: "environment" } };
          }
          // .. use this to start scanning.
          const html5QrCode = new Html5Qrcode(/* element id */ "reader");
          window.localStream = html5QrCode;
          html5QrCode
            .start(
              cameraId,
              {
                fps: 10, // Optional, frame per seconds for qr code scanning
                qrbox: { width: 250, height: 250 }, // Optional, if you want bounded box UI
              },
              (decodedText, decodedResult) => {
                // do something when code is read
                K.print(decodedText);
                try {
                  // const transDetail = Decrypt(content, transferPhrase);
                  const transDetail = Base64.decode(decodedText);
                  const decoded = JSON.parse(transDetail);
                  K.print(decoded);
                  // pretend it's taking really long
                  if (
                    decoded.type == "payment" &&
                    decoded.asset_code == K.asset &&
                    decoded.amount > 0 &&
                    decoded.memo.length > 0 &&
                    decoded.memo.length <= 28 &&
                    decoded.name.length > 0 &&
                    decoded.zid &&
                    decoded.mode == !K.devMode
                  ) {
                    clearCamera();
                    router.replace({
                      name: "Confirm",
                      params: { decoded: transDetail },
                    });
                  }
                } catch (err) {
                  K.error(err.message);
                }
              },
              (errorMessage) => {
                // parse error, ignore it.
              }
            )
            .catch((err) => {
              // Start failed, handle it.
              K.error(err);
            });
        }
      })
      .catch((err) => {
        // handle err
        router.replace({
          name: "Account",
        });
      });
    return { rtName, loaded, clearCamera };
  },
  beforeCreate: function () {
    // document.body.className = "scan";
  },
  beforeUnmount: async function () {
    // document.body.className = "normal";
    this.clearCamera();
  },
};
</script>

<style scoped>
.fullScreen {
  height: 100%;
  width: 100%;
  background: black;
}
video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translateX(-50%) translateY(-50%);
}
body {
  background: black;
}
</style>