<template>
  <div class="fullScreenSize" style="min-height: 100vh">
    <QRscan />
  </div>
</template>

<script>
import { ref } from "vue";
import { getPWADisplayMode } from "../composables/handlers";
import { useRouter } from "vue-router";
import QRscan from "../components/QRscan.vue";

export default {
  components: { QRscan },
  setup(props, context) {
    const loading = ref(true);

    const setReady = () => {
      if (loading.value == false) {
        loading.value = true;
      }
    };

    return {
      setReady,
    };
  },

  data() {
    const selected = { text: "outline", value: this.paintOutline };
    const router = useRouter();
    return {
      isValid: undefined,
      camera: "auto",
      result: null,
      fullscreen: (() => {
        if (getPWADisplayMode() == "standalone") {
          return false;
        } else {
          return false;
        }
      })(),
      selected,
      router,
    };
  },

  computed: {},

  watch: {},

  methods: {
    goBack() {
      this.router.replace({ name: "Home" });
    },
  },

  beforeRouteLeave: async function (to, from, next) {
    if (this.loading) {
      next(false);
    } else {
      try {
        window.localStream
          .stop()
          .then((ignore) => {
            // QR Code scanning is stopped.
          })
          .catch((err) => {
            // Stop failed, handle it.
          });
      } catch {}
      next();
    }
  },
};
</script>

<style scoped>
.fullScreenSize {
  height: 100%;
  width: 100%;
}
.nilScreen {
  height: 0vh;
  width: 0vw;
}
</style>
