<template>
  <div class="q-mx-none q-px-none q-pt-lg">
    <q-pull-to-refresh @refresh="refresh">
      <div v-if="assets.length > 0 && hasNetwork" class="q-ma-none q-pa-none">
        <div class="q-pa-none q-ma-none" style="max-width: 700px">
          <q-list>
            <q-item
              class="listItem"
              v-for="a in assets"
              :key="a.asset_code"
              :clickable="a.asset_code == K.asset"
              @click="handleAssetClick(a.asset_code)"
            >
              <q-item-section center avatar>
                <q-avatar
                  color="primary"
                  text-color="white"
                  :icon="iconSet[a.asset_code] || 'wifi_off'"
                />
              </q-item-section>

              <q-item-section>
                <q-item-label>
                  <p class="text-bold infoText">
                    {{ a.asset_code == K.asset ? K.asset_alias : a.asset_code }}
                    {{ a.alias }}
                  </p>
                </q-item-label>

                <q-item-label class="infoText">
                  <span v-if="a.amount">
                    <q-icon :name="iconSet[a.asset_code]" />
                  </span>
                  {{
                    a.amount
                      ? Number(a.amount).toLocaleString(undefined, {
                          minimumFractionDigits: a.precision,
                        })
                      : ""
                  }}
                </q-item-label>
              </q-item-section>

              <q-item-section side top>
                <q-item-label>
                  <q-icon :name="a.asset_code == K.asset ? 'send' : ''" />
                </q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </div>
        <div class="q-my-md q-mb-lg text-center btnStyle">
          <q-btn filled rounded color="primary" @click="toScan" no-caps>
            <span>Scan 嘟 </span
            ><q-icon class="q-mx-sm" name="qr_code_scanner" />
          </q-btn>
        </div>
      </div>
      <div v-if="assets.length >= 2 && hasNetwork">
        <q-footer>
          <div
            class="row q-px-lg q-mt-sm justify-between btnStyle"
            style="max-width: 700px"
          >
            <div class="q-my-sm q-mb-md text-center">
              <q-btn outline rounded color="white" @click="toReceive" no-caps>
                <div v-show="windowWidth >= 360">
                  <q-icon color="white" name="south_east" />
                  <q-icon color="white" :name="iconSet[K.asset]" />
                </div>
                <span :class="{ 'q-pl-sm': windowWidth >= 360 }"> Take 收</span>
              </q-btn>
            </div>
            <div class="q-my-sm q-mb-md text-center btnStyle">
              <q-btn outline rounded color="white" @click="toSend" no-caps>
                <span :class="{ 'q-pr-sm': windowWidth >= 360 }"> 畀 Give</span>
                <div v-show="windowWidth >= 360">
                  <q-icon color="white" :name="iconSet[K.asset]" />
                  <q-icon color="white" name="north_east" />
                </div>
              </q-btn>
            </div>
          </div>
        </q-footer>
      </div>
      <div v-else>
        <div v-show="!loaded && hasNetwork" style="text-align: center">
          <q-spinner-grid color="primary" size="2.5rem" />
        </div>
        <div v-show="!hasNetwork" style="text-align: center">
          <div class="q-pa-none q-ma-none" style="max-width: 700px">
            <q-list>
              <q-item
                class="listItem"
                v-for="a in assets"
                :key="a.asset_code"
                :clickable="a.asset_code == K.asset"
                @click="handleAssetClick(a.asset_code)"
              >
                <q-item-section center avatar>
                  <q-avatar
                    color="primary"
                    text-color="white"
                    :icon="iconSet[a.asset_code] || 'wifi_off'"
                  />
                </q-item-section>

                <q-item-section>
                  <q-item-label>
                    <p class="text-bold infoText">
                      {{
                        a.asset_code == K.asset ? K.asset_alias : a.asset_code
                      }}
                    </p>
                    <p class="text-bold infoText">{{ a.alias }}</p>
                  </q-item-label>
                </q-item-section>

                <q-item-section side top>
                  <q-item-label>
                    <q-icon :name="a.asset_code == K.asset ? 'send' : ''" />
                  </q-item-label>
                </q-item-section>
              </q-item>
            </q-list>
          </div>
          <q-spinner-radio color="primary" size="2.5rem" />
          <AppButton text="Retry 再試" :action="loadData" />
        </div>
      </div>
      <div
        v-show="loaded && assets.length == 0"
        class="q-mx-none q-px-none q-pt-lg"
      >
        <p class="assetText">No assets to show. Please Trade some first.</p>
        <p class="assetText">冇嘢睇。請先交換。</p>
        <AppButton text="Go 去" :action="toStore" />
      </div>
    </q-pull-to-refresh>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import AppButton from "./AppButton.vue";
import getAccount from "../composables/readAccount";
import {
  getPWADisplayMode,
  storage,
  readWallet,
  assetClaimHandler,
} from "../composables/handlers";
import { Decrypt, sdkEncoder } from "../composables/cipherHandler";
import {
  actions,
  trustLimit,
  auctioneer_zid,
} from "../composables/sdkOperations";
import * as K from "../constants/constants";
import { assetDefinition } from "../secrets/secrets";
import { iconSet } from "../constants/iconSet";
import * as SH from "../secrets/sdkHandler";
var axios = require("axios");

export default {
  components: { AppButton },
  emits: ["reloadAccount"],
  setup(props, context) {
    const loaded = ref(false);
    const router = useRouter();
    const { accounts, fetchAccount, records } = getAccount();
    const assets = ref([]);
    const hasNetwork = ref(true);

    const loadData = async () => {
      let listen = await storage.get("listen");
      if (listen) {
        await router.replace({ name: "Take" });
        return;
      }
      let wallet = await readWallet();
      loaded.value = false;
      assets.value = [];

      try {
        assets.value = (
          await JSON.parse(
            Decrypt(await storage.get("balances"), wallet.pack.key)
          )
        ).filter((account) => {
          return !account.nft;
        });
      } catch {
        assets.value = [];
      }

      if (window.navigator.onLine) {
        hasNetwork.value = true;
        try {
          const hasResult = await fetchAccount();
          let trans = [];
          if (hasResult) {
            assets.value = accounts.value.filter((account) => {
              return !account.nft;
            });
            if (!hasResult.hasAsset) {
              let addTrust = {
                action: actions.trust,
                xid: assetDefinition[K.asset].asset_issuer,
                asset_code: K.asset,
                amount: trustLimit.POS,
              };
              trans.push(addTrust);
            }

            await (async () => {
              const xid = sdkEncoder.zidToxid(wallet.disclose.zid);
              let url = `${SH.link}/accounts/${xid}/offers?order=asc`;
              let response = await axios.get(url);
              if (response.data) {
                let offers = await response.data._embedded.records;
                offers = await Promise.all(
                  offers.map(async (offer) => {
                    if (
                      offer.buying.asset_type != K.native &&
                      offer.selling.asset_type != K.native
                    ) {
                      if (
                        offer.buying.asset_issuer !=
                          sdkEncoder.zidToxid(auctioneer_zid) &&
                        offer.selling.asset_issuer !=
                          sdkEncoder.zidToxid(auctioneer_zid)
                      ) {
                        return offer;
                      } else {
                        let url = `${SH.link}/offers?selling_asset_type=credit_alphanum12&selling_asset_code=${offer.buying.asset_code}&selling_asset_issuer=${offer.buying.asset_issuer}&buying_asset_type=credit_alphanum12&buying_asset_code=${offer.selling.asset_code}&buying_asset_issuer=${offer.selling.asset_issuer}&order=desc`;
                        let responseOffer = await axios.get(url);
                        responseOffer = responseOffer.data;
                        if (responseOffer._embedded.records.length == 0) {
                          return offer;
                        }
                      }
                    }
                  })
                );
                offers = offers.filter((offer) => {
                  return offer;
                });
                let delTrans = offers.map((offer) => {
                  return {
                    action: actions.sellOffer,
                    xid: offer.id,
                    buy_asset_code: offer.buying.asset_code,
                    buy_asset_issuer: offer.buying.asset_issuer,
                    sell_asset_code: offer.selling.asset_code,
                    sell_asset_issuer: offer.selling.asset_issuer,
                    amount: "0",
                    price: "1",
                  };
                });
                trans = [...trans, ...delTrans];
              }
            })();

            const newTrans = await assetClaimHandler(trans);

            if (newTrans.length > 0) {
              await loadData();
            }
          }
          if (assets.value.length == 1 && assets.value[0].asset_code == K.gas) {
            K.print("call again to trust & claim asset");
            await loadData();
          }
          loaded.value = true;
        } catch (err) {
          K.error(err.message);
        }
      } else {
        let show = [
          { asset_code: "No network, please retry", alias: "冇網絡 請再試" },
        ];
        hasNetwork.value = false;
        assets.value = show;
      }
      context.emit("reloadAccount");
    };

    loadData();

    const toStore = () => {
      if (getPWADisplayMode() == "standalone") {
        router.replace({ name: "Trade" });
      } else {
        router.push({ name: "Trade" });
      }
    };

    const handleAssetClick = (asset) => {
      if (asset == K.asset) {
        router.replace({ name: "Transactions" });
      }
    };

    const toSend = () => {
      router.replace({ name: "Give" });
    };

    const toReceive = () => {
      router.replace({ name: "Take" });
    };

    const toScan = async () => {
      router.replace({ name: "Scan" });
    };

    return {
      accounts,
      fetchAccount,
      assets,
      toStore,
      toReceive,
      toScan,
      loaded,
      iconSet,
      K,
      loadData,
      toSend,
      hasNetwork,
      async refresh(done) {
        setTimeout(() => {
          loadData();
          done();
        }, 2500);
      },
      handleAssetClick,
    };
  },
  data: function () {
    return {
      windowWidth: 0,
      windowHeight: 0,
    };
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.getWindowWidth);
      window.addEventListener("resize", this.getWindowHeight);
      this.getWindowWidth();
      this.getWindowHeight();
    });
  },
  methods: {
    getWindowWidth(event) {
      this.windowWidth = document.documentElement.clientWidth;
    },
    getWindowHeight(event) {
      this.windowHeight = document.documentElement.clientHeight;
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.getWindowWidth);
    window.removeEventListener("resize", this.getWindowHeight);
  },
};
</script>

<style scoped>
.infoText {
  font-size: 1rem;
}
.assetText {
  font-size: 1.25rem;
}
.btnStyle button {
  min-width: 40%;
  font-size: 1rem;
}
.listItem {
  margin: auto none;
  padding: auto none;
}
</style>