const NodeRSA = require('node-rsa');
import { readWallet } from "../composables/handlers";
import { Decrypt } from "../composables/cipherHandler";
import { getPassword } from "../composables/cipherHandler";

export const publicHead = "-----BEGIN PUBLIC KEY-----";
export const privateHead = "-----BEGIN PRIVATE KEY-----";

export const publicTail = "-----END PUBLIC KEY-----";
export const privateTail = "-----END PRIVATE KEY-----";

export const newKey = () => {
    return new NodeRSA({ b: 512 });
};

export const getPrivateKey = (key) => {
    return key.exportKey('pkcs8-private');
};

export const getPublicKey = (key) => {
    return key.exportKey('pkcs8-public');
};

export const getPublicText = async() => {
    const key = await getKeys();
    const publ = getPublicKey(key);
    const publTxt = JSON.stringify((publ.replace(publicHead, "").replace(publicTail, "").trim()));
    return publTxt.slice(1, -1);
};

export const revertKey = (txt) => {
    txt = JSON.parse(`"${txt}"`);
    txt = `${rsa.publicHead}${txt}${rsa.publicTail}`;
    return txt;
}

export const initKey = (importKey) => {
    let key = new NodeRSA();
    if (importKey.search(publicHead) == 0) {
        key.importKey(importKey, 'pkcs8-public');
    } else if (importKey.search(privateHead) == 0) {
        key.importKey(importKey, 'pkcs8-private');
    };
    return key;
};

export const rsaEncrypt = (key, text, usePublic = true) => {
    let mes = { m: text, s: getPassword() };
    mes = JSON.stringify(mes)
    if (usePublic) {
        return key.encrypt(mes, 'base64');
    } else {
        return key.encryptPrivate(mes, 'base64');
    };
};

export const rsaDecrypt = (key, encrypted, usePrivate = true) => {
    try {
        if (usePrivate) {
            let de = JSON.parse(key.decrypt(encrypted, 'utf8'));
            return de.m
        } else {
            let de = JSON.parse(key.decryptPublic(encrypted, 'utf8'));
            return de.m
        };
    } catch {
        return "";
    };
};

export const getKeys = async() => {
    const wallet = await readWallet();
    const keytxt = Decrypt(wallet.pack.private, wallet.secure.zeed);
    const key = initKey(keytxt);
    return key;
};