import * as SH from "../secrets/sdkHandler";
import { sdkEncoder } from './cipherHandler';
var axios = require("axios");
import { actions, trustLimit, auctioneer_zid, Operations } from "../composables/sdkOperations";
import { readOffer } from "../composables/readOffer";
import { readWallet } from "../composables/handlers";
import * as K from "../constants/constants";

export const auctionSwap = async(asset_code, asset_issuer) => {
    let asset_issuer_xid = sdkEncoder.zidToxid(asset_issuer);

    let url = `${SH.link}/offers?buying_asset_type=credit_alphanum12&buying_asset_code=${asset_code}&buying_asset_issuer=${asset_issuer_xid}&order=desc`;
    let response = await axios.get(url);
    let responseJson = response.data;
    let offers;
    try {
        offers = responseJson._embedded.records;
        offers = offers.filter(offer => {
            return sdkEncoder.xidTozid(offer.seller) == auctioneer_zid
        });
        let pri = 1 / Number(trustLimit.MIN);
        let trans = [{
                action: actions.trust,
                xid: offers[0].selling.asset_issuer,
                asset_code: asset_code,
                amount: trustLimit.MIN,
            },
            {
                action: actions.buyOffer,
                xid: "0",
                buy_asset_code: asset_code,
                buy_asset_issuer: offers[0].selling.asset_issuer,
                sell_asset_code: asset_code,
                sell_asset_issuer: asset_issuer_xid,
                amount: trustLimit.MIN,
                price: pri.toFixed(0),
            },
        ];

        const wallet = await readWallet();

        let myOffers = await readOffer(wallet.disclose.zid, true);
        myOffers = myOffers.filter(offer => {
            return offer.buying.asset_code == asset_code &&
                offer.selling.asset_code == asset_code &&
                offer.selling.asset_issuer == asset_issuer_xid &&
                offer.buying.asset_issuer == offers[0].selling.asset_issuer
        });

        if (myOffers.length == 0) {
            await Operations(trans, asset_code);
        };

    } catch (err) {
        K.error(err);
    };

};